namespace danfo.build.code.keywords {

  /**
   * A custom transformer supposed to manage the use of what's ES3 keywords but allowed in ES5.
   */
  export function transformKeywords(context: typescript.TransformationContext, es3Keywords: string[], ts: typeof import('typescript')) {

    return transformSourceFile;

    function transformSourceFile(sourceFileNode: typescript.SourceFile): typescript.SourceFile {
      const { factory } = context;
      const transformed = ts.visitEachChild(sourceFileNode, visitNodeReplaceES3Keywords, context);
      if (!transformed) {
        return sourceFileNode;
      }
      else {
        return transformed;
      }

      function visitNodeReplaceES3Keywords(node: typescript.Node): typescript.Node | undefined {
        const nestedReplacements = ts.visitEachChild(node, visitNodeReplaceES3Keywords, context);
        node = nestedReplacements || node;

        if (ts.isPropertyAccessExpression(node) && ts.isIdentifier(node.name) && es3Keywords.indexOf(node.name.text) >= 0) {
          const replacement = factory.createElementAccessExpression(
            node.expression,
            factory.createStringLiteral(node.name.text));

          const mappedReplacement = ts.setTextRange(replacement, node);
          return mappedReplacement;
        }
        else if (ts.isPropertyAccessExpression(node) && ts.isIdentifier(node.name) && es3Keywords.indexOf(node.name.text) >= 0) {
          const replacement = factory.createPropertyAssignment(
            node.name.text,
            node.expression);

          const mappedReplacement = ts.setTextRange(replacement, node);
          return mappedReplacement;
        }

        return nestedReplacements;
      }
    }
  }
}