namespace danfo.build.code {

  const es3Keywords = [
    'default', 'delete', 'import', 'export', 'class',
    'break', 'for', 'new', 'true', 'with', 'continue', 'function',
    'null', 'typeof', 'else', 'if', 'return', 'var', 'false', 'in', 'this', 'while'
  ];

  export var defineAmd: Function;
  export var defineAmdWrappedText: string | undefined;

  export declare namespace codeBuilder {

    /**
     * Pass an object with the options, it will be populated with these values as the code is built.
     */
    type DebugState = {
      ts: typeof import('typescript');
      documentRegistry: typescript.DocumentRegistry;
      languageServiceHost: typescript.LanguageServiceHost & typescript.ModuleResolutionHost,
      languageService: typescript.LanguageService;
      moduleResolutionCache: typescript.ModuleResolutionCache;
      program: typescript.Program;
      scriptInputs: SourceFileOrSpan[];
      settings: typescript.CompilerOptions;
      configSourcedFiles: string[];
      tsLibPath: string;
    }
  }

  export function codeBuilder(options: codeBuilder.BuilderOptions & { debugState?: Partial<codeBuilder.DebugState> }): Builder {
    const { fileSystem, precachedFixedSource, debugState } = options;

    type CachedState = {
      cachedScriptFileNames?: string[],
      cachedFileEntries?: { [resolvedFile: string]: CachedFileEntry };
      cachedFileStats?: { [resolvedFile: string]: FileSystem.Stat };
      cachedFileContent?: { [resolvedFile: string]: string };
      cacheScriptSnapshots?: { [resolvedFile: string]: typescript.IScriptSnapshot };
      getDirectoriesByPath?: { [resolvedDirPath: string]: string[] };
      cachedResolvedDefaultLibFileName?: string;
    };

    const ts = importTypeScript();
    if (debugState) debugState.ts = ts;
    // these variables would be re-populated on incremental rebuilds

    let inputs: BuilderInputs = {
      sources: [],
      resolvedProjectDir: '.'
    };
    let scriptInputs: SourceFileOrSpan[] = [];
    if (debugState) debugState.scriptInputs = scriptInputs;
    const baseSettings = createDefaultSettings();
    let settings = baseSettings;
    if (debugState) debugState.settings = settings;
    
    let configSourcedFiles: string[] = [];
    if (debugState) debugState.configSourcedFiles = configSourcedFiles;



    // memoizing results for repetitive requests during build
    // (will be re-populated before a rebuild starts)
    let cachedState: CachedState = {};
    let fileCache: { [resolvedFileName: string]: CachedFileEntry } = {};
    let cache = createKeyCache(cachedState, null);

    let buildState!: ReturnType<typeof initializeBuildState>;


    const builder: Builder = {
      build: triggerBuild
    };

    return builder;

    function resolveJoinPathInProject(path1: string, path2?: string): string {
      let result = fileSystem.resolvePath(inputs.resolvedProjectDir, path1);
      if (path2) result = fileSystem.resolvePath(result, path2);
      return result;
    }

    function getProjectRelativeFileName(path1: string, path2?: string): string {
      const resolvedPath = resolveJoinPathInProject(path1, path2);
      const relative = fileSystem.getRelativePath(inputs.resolvedProjectDir, resolvedPath);
      return relative;
    }

    function promoteCaches(cachedState: CachedState) {
      const deleteEntryKeys: string[] =[];
      for (var k in fileCache) {
        const entry = fileCache[k];
        if (entry && typeof entry.unusedCount === 'number') {
          entry.unusedCount++;
          if (entry.unusedCount > 3) {
            deleteEntryKeys.push(k);
          }
        }
      }

      for (const k of deleteEntryKeys) {
        delete fileCache[k];
      }


      return {

      };
    }

    function initializeBuildState() {

      // serves as both ts.LanguageServiceHost and ts.ModuleResolutionHost
      const langHost = {
        getCompilationSettings: hostGetCompilationSettings,
        getScriptFileNames: hostGetScriptFileNames,
        getScriptVersion: hostGetScriptVersion,
        getScriptSnapshot: hostGetScriptSnapshot,
        getCurrentDirectory: hostGetCurrentDirectory,
        getDirectories: hostGetDirectories,
        getDefaultLibFileName: hostGetDefaultLibFileName,
        fileExists: hostFileExists,
        readFile: hostReadFile,
        getCanonicalFileName: hostGetCanonicalFileName,
        useCaseSensitiveFileNames: () => true
      };
      if (debugState) debugState.languageServiceHost = langHost;

      // two functions overridden, to cache lib.d.ts more aggressively (in tests)
      const baseDocumentRegistry = ts.createDocumentRegistry(
        true /* useCaseSensitiveFileNames */,
        inputs.resolvedProjectDir /* currentDirectory */
      );

      const documentRegistry: typescript.DocumentRegistry = {
        ...baseDocumentRegistry,
        acquireDocument: cachedAcquireDocument,
        acquireDocumentWithKey: cachedAcquireDocumentWithKey
      };
      if (debugState) debugState.documentRegistry = documentRegistry;

      const langService = ts.createLanguageService(
        langHost,
        documentRegistry,
        false /* syntaxOnly */);
      if (debugState) debugState.languageService = langService;

      // TODO: resolve from actual TypeScript used (require.resolve?)
      const tsLibPath = fileSystem.resolvePath(inputs.resolvedProjectDir, 'node_modules/typescript/lib');
      if (debugState) debugState.tsLibPath = tsLibPath;

      let moduleResolutionCache = ts.createModuleResolutionCache(
        inputs.resolvedProjectDir,
        path => getProjectRelativeFileName(path));
      if (debugState) debugState.moduleResolutionCache = moduleResolutionCache;

      return {
        langHost,
        documentRegistry,
        langService,
        tsLibPath,
        moduleResolutionCache
      };

      function cachedAcquireDocument(
        fileName: string,
        compilationSettings: typescript.CompilerOptions,
        scriptSnapshot: typescript.IScriptSnapshot,
        version: string,
        scriptKind: typescript.ScriptKind) {
        if (precachedFixedSource) {
          const resolvedFileName = fileSystem.resolvePath(inputs.resolvedProjectDir, fileName);
          const entry = precachedFixedSource(resolvedFileName);
          if (entry && entry.snapshot === scriptSnapshot)
            return entry.source;
        }

        return baseDocumentRegistry.acquireDocument(
          fileName, compilationSettings, scriptSnapshot, version, scriptKind);
      }

      function cachedAcquireDocumentWithKey(
        fileName: string,
        path: import('typescript').Path,
        compilationSettings: typescript.CompilerOptions,
        key: import('typescript').DocumentRegistryBucketKey,
        scriptSnapshot: typescript.IScriptSnapshot,
        version: string,
        scriptKind: typescript.ScriptKind) {
        if (precachedFixedSource) {
          const resolvedFileName = fileSystem.resolvePath(inputs.resolvedProjectDir, fileName);
          const entry = precachedFixedSource(resolvedFileName);
          if (entry && entry.snapshot === scriptSnapshot)
            return entry.source;
        }

        return baseDocumentRegistry.acquireDocumentWithKey(fileName, path, compilationSettings, key, scriptSnapshot, version, scriptKind);
      }
    }

    function triggerBuild(newInputs: BuilderInputs): BuildResult {

      if (!buildState)
        buildState = initializeBuildState();

      // TODO: promote relevant caches
      cachedState = promoteCaches(cachedState);

      inputs = newInputs;
      cache = createKeyCache(cachedState, null);

      scriptInputs = populateScriptFileNamesFromInputs();
      if (debugState) debugState.scriptInputs = scriptInputs;

      // read and combine any tsconfig.json used as input
      const tsCmdline = populateSettingsIncludingInputs(scriptInputs);

      settings = tsCmdline?.options || baseSettings;

      // override/manipulate settings, especially in case tsconfig is read from file system
      if (typeof newInputs.handleSettings === 'function') {
        const updatedSettings = newInputs.handleSettings(settings);
        if (updatedSettings)
          settings = updatedSettings;
      }
      if (debugState) debugState.settings = settings;

      if (tsCmdline?.fileNames) {
        configSourcedFiles = tsCmdline.fileNames;
        if (debugState) debugState.configSourcedFiles = configSourcedFiles;
      }

      // TODO: validate if this is needed anyway?
      buildState.moduleResolutionCache = ts.createModuleResolutionCache(
        newInputs.resolvedProjectDir,
        path =>
          getProjectRelativeFileName(path));
      if (debugState) debugState.moduleResolutionCache = buildState.moduleResolutionCache;


      /**
       * Matching source and target at a file level.
       * Note either source or target may be missing:
       * compilation could have failed, or extra files generated/injected without actual source on disk.
       */
      type FileEntry = {
        source?: {
          resolvedFileName: string;
          node: typescript.SourceFile;
          dependencies?: {
            imports: findDependencies.Dependency[],
            exports: findDependencies.Export[]
          };
        };

        target?: {
          resolvedFileName: string;
          sourcemap?: { fileName: string, content: string };
          output?: string;
          typings?: { fileName: string, content: string };
        };
      };
      
      // These will be populated from inputs and outputs, including match one to another.
      // That matching is important when multiple outputs need to be injected to corresponding places in HTML.
      const bySource: { [relativeSourceFileName: string]: FileEntry } = {};
      const byTarget: { [relativeTargetFileName: string]: FileEntry } = {};
      const fileEntryList: FileEntry[] = [];

      const program = buildState.langService.getProgram();
      if (debugState) debugState.program = program!;

      if (!program) {
        // TODO: how can it get here???
        return {
          emits: [],
          errors: [],
          imports: []
        };
      }

      // by now there may be more sources resolved by TypeScript itself
      // (imports, triple-slash and whatnot)
      populateSourcesFromProgram();


      type ModuleDependencyDetails = {
        moduleName: string;
        importedBy?: ModuleDependencyImportFromFile[];
        exporting?: {
          sourceFile: typescript.SourceFile;
          exports: findDependencies.Export[];
        }
      }

      type ModuleDependencyImportFromFile = {
        source: {
          sourceFile: typescript.SourceFile
        };
        dependencies: findDependencies.Dependency[]
      };


      const hoistHelpersTransformer = hoistHelpers({ ts });
      const detectAmdDefinesTransformer = convertAmd({
        ts,
        program,
        generateModuleName: sourceFileName => getProjectRelativeFileName(sourceFileName),
        resolveModuleName: (sourceFileName, moduleName) => {
          if (moduleName.charCodeAt(0) === CharCodes.Dot
            || moduleName.charCodeAt(0) === CharCodes.Slash
            || moduleName.charCodeAt(0) === CharCodes.Backslash)
            return fileSystem.resolvePath(inputs.resolvedProjectDir, sourceFileName + '/../' + moduleName);
          else if (moduleName.charCodeAt(0) === CharCodes.Tilda)
            return fileSystem.resolvePath(inputs.resolvedProjectDir, moduleName);
          else
            return moduleName;
        }
      });

      const codeBuilderResult = runEmit();

      const externalImportList: Import[] = [];
      for (const mod of detectAmdDefinesTransformer.modules) {
        if (mod.importedBy && !mod.exportedBy) {
          // TODO: resolve external import, if succeeded, process in a separate, relaxed language service

          externalImportList.push({
            reference: mod.moduleName
          });
        }
      }

      const buildResult = {
        ...codeBuilderResult,
        emits: [...codeBuilderResult.emits],
        imports: externalImportList
      };


      if (detectAmdDefinesTransformer.defines?.length) {

        const preloadRootSources: string[] = [];
        const preloadRootSourceNames: string[] = [];
        for (const inp of newInputs.sources) {
          // TODO: handle directory (load index.js/ts)

          for (const defn of detectAmdDefinesTransformer.defines) {
            if (defn.sourceFile.fileName === inp.resolvedPath) {
              const projRelativePath = getProjectRelativeFileName(inp.resolvedPath);
              if (preloadRootSourceNames.indexOf(projRelativePath) < 0) {
                preloadRootSourceNames.push(projRelativePath);
                preloadRootSources.push('define.require(' + JSON.stringify(projRelativePath) + ');');
              }
            }
          }
        }

        if (!defineAmdWrappedText)
          defineAmdWrappedText = 'var define = (function() { return defineAmd();\n' + defineAmd + '\n})()';

        const defineFunctionEmit = generateHelperEmitWithPerLineMaps({
          name: '__define.js',
          text: defineAmdWrappedText
        });
        
        buildResult.emits.unshift(defineFunctionEmit);


        if (preloadRootSources.length) {
          const moduleLoad: Emit = generateHelperEmitWithPerLineMaps({
            name: '__moduleLoad.js',
            text: preloadRootSources.join('\n')
          });
          moduleLoad.injectAfter = true;

          buildResult.emits.push(moduleLoad);
        }
      }

      return buildResult;

      function populateScriptFileNamesFromInputs() {
        let encounteredTsconfigs: SourceFileOrSpan[] | undefined;
        const allSources: SourceFileOrSpan[] = [];
        for (const src of inputs.sources) {
          if (src.type as string === 'html' || src.type === 'css' || !src.type) continue;

          if (src.type === 'tsconfig') {
            (encounteredTsconfigs || (encounteredTsconfigs = [])).push(src);
          }
          else {
            allSources.push(src);
          }
        }

        if (encounteredTsconfigs && encounteredTsconfigs.length === 1)
          allSources.unshift(encounteredTsconfigs[0]);

        return allSources;

        // TODO: do we need any special handling for packages?
        // (especially when in node_modules?)
        // function populateFromPackage(packageJsonPath: string) {

        // }
      }

      function populateSourcesFromProgram() {
        for (const sourceFile of program!.getSourceFiles()) {
          const resolvedFileName = resolveJoinPathInProject(sourceFile.fileName);
          const relativeFileName = getProjectRelativeFileName(resolvedFileName);
          let entry = bySource[relativeFileName];
          if (entry) {
            if (!entry.source) {
              entry.source = {
                resolvedFileName,
                node: sourceFile
              };
            }
            entry.source.node = sourceFile;
          }
          else {
            entry = {
              source: {
                resolvedFileName,
                node: sourceFile
              }
            };
            bySource[relativeFileName] = entry;
            fileEntryList.push(entry);
          }
        }
      }

      function runEmit() {
        const emitResult = program!.emit(
          void 0 /* targetSourceFile */,
          onFileEmit,
          void 0 /* cancellationToken */,
          false /* emitOnlyDtsFiles */,
          {
            before: [],
            after: [
              context => keywords.transformKeywords(context, es3Keywords, ts),
              hoistHelpersTransformer.transform,
              detectAmdDefinesTransformer.transform
            ],
            afterDeclarations: []
          } /* customTransformers */);
        
        const errors: BuildError[] = [];
        for (const emit of fileEntryList) {
          if (emit.source && emit.target) {
            if (!/\bnode_modules\b/.test(emit.source.resolvedFileName)) {
              addDiagnosticErrors(program!.getSyntacticDiagnostics(emit.source.node));
              addDiagnosticErrors(program!.getSemanticDiagnostics(emit.source.node));
            }
          }
        }

        addDiagnosticErrors(program!.getGlobalDiagnostics());
        addDiagnosticErrors(program!.getOptionsDiagnostics())

        addDiagnosticErrors(emitResult.diagnostics);

        // if (emitResult.emitSkipped) {
        //   errors.push({
        //     code: 'TS',
        //     message: 'Emit was skipped by TypeScript build.'
        //   });
        // }

        let emits = fileEntryList.map(entry => {
          if (entry.target) {
            return {
              resolvedSourcePath: entry.source ? entry.source.resolvedFileName : entry.target?.resolvedFileName,
              sourcemap: entry.target.sourcemap,
              typings: entry.target.typings,
              output: entry.target.output
            };
          }
          else if (entry.source) {
            // TODO: adjust this simple heuristic
            // A lot of .d.ts typings files are actually taking part in a typical build process
            // -- including standard lib.d.ts and huge react, node typings and such.
            // These are not relevant and should not be included.
            if (!/node_modules/.test(entry.source.resolvedFileName)) {
              const text = entry.source.node.getFullText();
              if (typeof text === 'string') {
                return generatePlainEmitWithEmptyFile(
                  getProjectRelativeFileName(entry.source.resolvedFileName),
                  text);
              }
            }
          }
        }).filter(entry => entry) as Emit[];

        const namedHelpers: { [name: string]: boolean } = {};

        const injectHelpers = processInjectedHelpers();
        if (injectHelpers)
          emits = [...injectHelpers, ...emits];

        return { emits, errors };

        function processInjectedHelpers() {
          const injectHelpers: typeof emits = [];
          hoistHelpersTransformer.emitHelpers.sort((h1, h2) =>
            (h1.emitHelper.priority || 0) - (h2.emitHelper.priority || 0) // sort by priority, then by name
            || (h1.emitHelper.name > h2.emitHelper.name ? +1 : h1.emitHelper.name < h2.emitHelper.name ? -1 : 0));

          const emitHelpersOrdered = [...hoistHelpersTransformer.emitHelpers].sort((h1, h2) => h1.fileName > h2.fileName ? +1 : h1.fileName < h2.fileName ? -1 : 0);

          for (const h of emitHelpersOrdered) {
            if (namedHelpers[h.emitHelper.name]) continue;
            namedHelpers[h.emitHelper.name] = true;
            injectHelpers.push(generateHelperEmitWithPerLineMaps(h.emitHelper));
          }

          return injectHelpers;
        }

        function addDiagnosticErrors(diagList: readonly typescript.Diagnostic[] | undefined) {
          if (diagList) {
            for (const diag of diagList) {
              if (diag.file && /\bnode_modules\b/i.test(diag.file.fileName))
                continue;

              errors.push(createErrorFromDiagnostic(diag));
            }
          }
        }

        function createErrorFromDiagnostic(diag: typescript.Diagnostic): BuildError {
          const err: BuildError = {
            resolvedSourcePath: diag.file ? diag.file.fileName : void 0,
            code: 'TS' + String(diag.code),
            message: typeof diag.messageText === 'string' ? diag.messageText : diag.messageText.messageText,
            typescriptDetails: diag,
            format: formatDiagnostic
          };

          return err;

          function formatDiagnostic(overrideBaseDir?: string) {
            return danfo.node.formatBuildError(
              err,
              {
                typescriptForFormatting: ts,
                projRoot: overrideBaseDir ? fileSystem.resolvePath(inputs.resolvedProjectDir, overrideBaseDir) : inputs.resolvedProjectDir,
                fileSystem
              });
          }
        }
      }

      /**
       * Callback invoked by TypeScript on every file emitted.
       * Note an important parameter here is `sourceFiles`, allowing to map an emitted output back to the original source file.
       * 
       * It's usually 1 file, can be multiple when TypeScript's own bundling is in use (--outFile tsconfig parameter)
       */
      function onFileEmit(
        fileName: string,
        data: string,
        writeByteOrderMark: boolean,
        onError?: (message: string) => void,
        sourceFiles?: readonly typescript.SourceFile[]) {

        const resolvedFileName = resolveJoinPathInProject(inputs.resolvedProjectDir, fileName);
        const relativeFileName = getProjectRelativeFileName(resolvedFileName);
        const isTypings = codeBuilder.isTypingsFilename(resolvedFileName);
        const isSourcemap = codeBuilder.isSourcemapFilename(resolvedFileName);
        const isJavaScript = codeBuilder.hasJavascriptFileExtension(resolvedFileName);
        if (!isTypings && !isSourcemap && !isJavaScript) {
          if (sourceFiles) {
            // TODO: that may be something intended?
          }
          else {
            // TODO: that is likely junk or cache files
          }

          return;
        }

        let entry = byTarget[relativeFileName];
        if (!entry) {
          // find possible source file for this emit
          if (sourceFiles) {
            for (const sourceFileEntry of sourceFiles) {
              const resolvedSourceFileName = resolveJoinPathInProject(inputs.resolvedProjectDir, sourceFileEntry.fileName);
              const relativeSourceFileName = getProjectRelativeFileName(resolvedSourceFileName);
              entry = bySource[relativeSourceFileName];
              // known source found
              if (entry) break;
            }
            if (!entry) {
              // source found, but not known, let's add it
              const useFirstSource = sourceFiles[0];
              if (useFirstSource) {
                const resolvedSourceFileName = resolveJoinPathInProject(inputs.resolvedProjectDir, useFirstSource.fileName);
                entry = {
                  source: {
                    resolvedFileName: resolvedSourceFileName,
                    node: useFirstSource
                  }
                };
                fileEntryList.push(entry);
              }
            }
          }

          if (!entry) {
            // source not found: this has to be an emit without original source
            entry = {
              target: {
                resolvedFileName
              }
            };
            fileEntryList.push(entry);
          }

          byTarget[relativeFileName] = entry;
        }

        if (!entry.target) {
          entry.target = {
            resolvedFileName
          };
        }

        if (isTypings)
          entry.target!.typings = { fileName: resolvedFileName, content: data };
        else if (isSourcemap)
          entry.target!.sourcemap = { fileName: resolvedFileName, content: data };
        else
          entry.target!.output = data;
      }
    }

    function createDefaultSettings() {
      const settings = ts.getDefaultCompilerOptions();
      settings.jsx = ts.JsxEmit.React;
      settings.checkJs = true;
      settings.allowJs = true;
      settings.sourceMap = true;
      settings.inlineSources = true;
      settings.module = ts.ModuleKind.AMD;
      settings.moduleResolution = ts.ModuleResolutionKind.Node10;
      settings.declaration = true;
      settings.newLine = ts.NewLineKind.LineFeed;
      settings.target = ts.ScriptTarget.ES5;
      settings.skipDefaultLibCheck = true;
      settings.resolveJsonModule = true;

      // settings.lib = (settings.lib || []);
      // // 'dom.iterable'
      // // 'scripthost'
      // // 'dom', 'webworker'
      // for (const lib of ['esnext']) {
      //   if (settings.lib.indexOf(lib) < 0)
      //     settings.lib.push(lib);
      // }

      // settings.incremental = true;
      // settings.composite = true;
      // settings.tsBuildInfoFile = projectDir + '/.cache.tsbuildinfo';
      settings.strict = true;

      settings.outDir = inputs.resolvedProjectDir + '/dist';

      return settings;
    }

    function populateSettingsIncludingInputs(sources: SourceFileOrSpan[]): import('typescript').ParsedCommandLine | undefined {
      let result: import('typescript').ParsedCommandLine | undefined;
      for (const src of sources) {
        if (src.type === 'tsconfig') {
          const cfgDir = resolveJoinPathInProject(src.resolvedPath, '..');
          const resolvedNormalizedPath = getProjectRelativeFileName(src.resolvedPath);
          const configJson = ts.readJsonConfigFile(
            resolvedNormalizedPath,
            filePath => fileSystem.readFile(filePath === resolvedNormalizedPath ? src.resolvedPath : filePath));
          const host: import('typescript').ParseConfigHost = {
            useCaseSensitiveFileNames: true, // TODO: rely on underlying OS?
            readDirectory: (...args) => readDirectory(inputs.resolvedProjectDir, fileSystem, ...args),
            fileExists: filePath => {
              const ex = !!fileSystem.stat(
                filePath === resolvedNormalizedPath ?
                  src.resolvedPath :
                  fileSystem.resolvePath(cfgDir, filePath));
              return ex;
            },
            readFile: filePath => {
              const data = fileSystem.readFile(
                filePath === resolvedNormalizedPath ?
                  src.resolvedPath :
                  fileSystem.resolvePath(cfgDir, filePath));
              return data;
            }
            // TODO: trace?(s: string): void
          };
          const parsedConfigJson = ts.parseJsonSourceFileConfigFileContent(
            configJson,
            host,
            cfgDir,
            result && result.options || baseSettings,
            src.resolvedPath);
          
          // make all file paths absolute
          if (parsedConfigJson.fileNames) {
            for (let i = 0; i < parsedConfigJson.fileNames.length; i++) {
              parsedConfigJson.fileNames[i] = fileSystem.resolvePath(cfgDir, parsedConfigJson.fileNames[i]);
            }
          }

          parsedConfigJson.fileNames = joinArrays(result?.fileNames, parsedConfigJson.fileNames)!;
          parsedConfigJson.projectReferences = joinArrays(result?.projectReferences, parsedConfigJson.projectReferences);
          // TODO: parsedConfigJson.wildcardDirectories
          result = parsedConfigJson;
        }
      }
      return result;

      function joinArrays<T, TArray extends ConcatArray<T>>(array1: TArray | undefined, array2: TArray | undefined): TArray | undefined {
        if (!array1)
          return array2;
        else if (!array2)
          return array1;
        else
          return (array1 as any as T[]).concat(array2) as any as TArray;
      }
    }

    type CachedFileEntry = {
      stat?: FileSystem.Stat;
      version: string;
      content?: string;
      snapshot?: typescript.IScriptSnapshot;
      unusedCount: number;
    }

    function getFileStatDirect(resolvedFileName: string) {
      let stat = fileSystem.stat(resolvedFileName);

      if (!stat || stat.isDirectory) {
        const defaultLib = hostGetDefaultLibFileName(settings);
        const parentDirPath = resolveJoinPathInProject(resolvedFileName, '..');
        if (parentDirPath === defaultLib
          || parentDirPath === resolveJoinPathInProject(defaultLib, '..')
          || parentDirPath.toLowerCase() === defaultLib.toLowerCase()
          || parentDirPath.toLowerCase() === resolveJoinPathInProject(defaultLib, '..').toLowerCase()) {
          const libFileName = getBaseName(resolvedFileName);
          const redirectToStandardLibPath = ts.resolveDefaultLibFromOfficial(libFileName);
          if (redirectToStandardLibPath) {
            stat = fileSystem.stat(redirectToStandardLibPath);
          }
        }
      }

      return stat;
    }

    function getFileContentDirect(resolvedFileName: string) {
      let content = fileSystem.readFile(resolvedFileName);
      if (typeof content !== 'string') {
        const defaultLib = hostGetDefaultLibFileName(settings);
        const parentDirPath = resolveJoinPathInProject(resolvedFileName, '..');
        if (parentDirPath === defaultLib
          || parentDirPath === resolveJoinPathInProject(defaultLib, '..')
          || parentDirPath.toLowerCase() === defaultLib.toLowerCase()
          || parentDirPath.toLowerCase() === resolveJoinPathInProject(defaultLib, '..').toLowerCase()) {
          const libFileName = getBaseName(resolvedFileName);
          const redirectToStandardLibPath = ts.resolveDefaultLibFromOfficial(libFileName);
          if (redirectToStandardLibPath)
            content = fileSystem.readFile(redirectToStandardLibPath);
        }

      }
      return typeof content === 'string' ? content : null;
    }

    function getScriptSnapshotDirect(resolvedFileName: string) {
      if (typeof precachedFixedSource === 'function') {
        const entry = precachedFixedSource(resolvedFileName);
        if (entry) return entry.snapshot;
      }

      let fileContent = getFileContentDirect(resolvedFileName);
      if (typeof fileContent !== 'string') {
        const defaultLib = hostGetDefaultLibFileName(settings);
        const parentDirPath = resolveJoinPathInProject(resolvedFileName, '..');
        if (parentDirPath === defaultLib
          || parentDirPath === resolveJoinPathInProject(defaultLib, '..')) {
          const libFileName = getBaseName(resolvedFileName);
          const redirectToStandardLibPath = ts.resolveDefaultLibFromOfficial(libFileName);
          if (redirectToStandardLibPath) {
            fileContent = getFileContentDirect(redirectToStandardLibPath);
          }
        }

        return null;
      }

      return createScriptSnapshot(resolvedFileName, fileContent);
    }

    function getCachedFileEntry(resolvedFileName: string): CachedFileEntry | null {
      const entry = cache.cachedOrPopulate<CachedFileEntry>({
        cacheField: 'cachedFileEntries',
        key: resolvedFileName,
        populate: () => {
          const stat = getFileStatDirect(resolvedFileName);
          if (!stat) {
            return fileCache[resolvedFileName] = {
              unusedCount: 0,
              version: 'missing'
            };
          }

          const oldEntry = fileCache[resolvedFileName];
          if (oldEntry && typeof oldEntry.unusedCount === 'number') {
            if (oldEntry.stat?.length === stat?.length && oldEntry.stat?.timestamp === stat?.timestamp) {
              oldEntry.unusedCount = 0;
              return oldEntry;
            }
          }

          const content = getFileContentDirect(resolvedFileName) || undefined;

          if (oldEntry && oldEntry.content === content) {
            // timestamp changed, but content remained - retain everything
            oldEntry.stat = stat;
            oldEntry.unusedCount = 0;
            return oldEntry;
          }

          const version = deriveFileVersionFromStat(stat);
          const snapshot = getScriptSnapshotDirect(resolvedFileName) || undefined;

          return fileCache[resolvedFileName] = {
            unusedCount: 0,
            stat,
            version,
            content,
            snapshot
          };
        }
      });

      return entry.completed && entry.success && entry.result || null;
    }

    function getFileStat(resolvedFileName: string): FileSystem.Stat | null {
      const entry = cache.cachedOrPopulate<FileSystem.Stat | null>({
        cacheField: 'cachedFileStats',
        key: resolvedFileName,
        populate: () => {
          const entry = getCachedFileEntry(resolvedFileName);
          return entry?.stat || null;
        }
      });

      return entry.completed && entry.success && entry.result || null;
    }

    function getFileContent(resolvedFileName: string): string | null {
      const entry = cache.cachedOrPopulate<string | null>({
        cacheField: 'cachedFileContent',
        key: resolvedFileName,
        populate: () => {
          const entry = getCachedFileEntry(resolvedFileName);
          return typeof entry?.content === 'string' ? entry.content : null;
        }
      });

      return entry.completed && entry.success && entry.result || null;
    }

    function hostGetCompilationSettings(): typescript.CompilerOptions {
      return settings;
    }

    function hostGetScriptFileNames(): string[] {
      const entry = cache.cachedOrPopulate<string[]>({
        cacheField: 'cachedScriptFileNames',
        populate: () => {
          const sources: string[] = [];
          for (const src of scriptInputs) {
            if (src.type !== 'tsconfig') {
              sources.push(src.resolvedPath);
            }
          }
          if (configSourcedFiles) {
            for (const src of configSourcedFiles) {
              const resolvedPath = resolveJoinPathInProject(src);
              // const relativePath = getProjectRelativeFileName(resolvedPath);
              sources.push(resolvedPath);
            }
          }
          return sources;
        }
      });

      return !entry.completed || !entry.success ? [] : entry.result;
    }

    function hostGetScriptVersion(fileName: string): string {
      const resolvedFileName = resolveJoinPathInProject(fileName);
      if (typeof precachedFixedSource === 'function') {
        const entry = precachedFixedSource(resolvedFileName);
        if (entry) return 'precached';
      }


      const entry = getCachedFileEntry(resolvedFileName);
      return entry?.version || 'cache-miss';
    }

    function deriveFileVersionFromStat({ timestamp, length }: { timestamp: number, length: number }) {
      return length + '@' + timestamp;
    }
    
    function hostFileExists(fileName: string): boolean {
      const resolvedFileName = resolveJoinPathInProject(fileName);
      const stat = getFileStat(resolvedFileName);
      return !!stat && !stat.isDirectory;
    }

    function hostGetScriptSnapshot(fileName: string): typescript.IScriptSnapshot | undefined {
      const resolvedFileName = resolveJoinPathInProject(fileName);
      if (typeof precachedFixedSource === 'function') {
        const entry = precachedFixedSource(resolvedFileName);
        if (entry) return entry.snapshot;
      }

      const entry = cache.cachedOrPopulate<typescript.IScriptSnapshot | null>({
        cacheField: 'cacheScriptSnapshots',
        key: resolvedFileName,
        populate: () => {
          const entry = getCachedFileEntry(resolvedFileName);
          return entry?.snapshot ||null;
        }
      });

      if (entry.completed && entry.success) return entry.result || void 0;
      else return void 0;
    }

    function hostReadFile(fileName: string): string | undefined {
      const resolvedFileName = resolveJoinPathInProject(fileName);
      const fileContent = getFileContent(resolvedFileName);
      return typeof fileContent === 'string' ? fileContent : void 0;
    }

    function hostGetCanonicalFileName(fileName: string): string {
      return getProjectRelativeFileName(fileName);
    }

    function hostGetCurrentDirectory(): string {
      return inputs.resolvedProjectDir;
    }

    let emptyDirectoryList: string[] | undefined;
    function hostGetDirectories(dirPath: string): string[] {
      const resolvedDirPath = resolveJoinPathInProject(dirPath);
      const entry = cache.cachedOrPopulate<string[] | null>({
        cacheField: 'getDirectoriesByPath',
        key: resolvedDirPath,
        populate: () => {
          const dirs = fileSystem.readDirectory(resolvedDirPath);
          return dirs || null;
        }
      });

      return (entry.completed && entry.success ? entry.result : null) ||
        emptyDirectoryList || (emptyDirectoryList = []);
    }

    function hostGetDefaultLibFileName(options: typescript.CompilerOptions): string {
      const entry = cache.cachedOrPopulate<string>({
        cacheField: 'cachedResolvedDefaultLibFileName',
        populate: () => hostGetCanonicalFileName(fileSystem.resolvePath(buildState.tsLibPath, ts.getDefaultLibFileName(options)))
      });

      return entry.completed && entry.success ? entry.result : 'lib.d.ts';
    }

    function createScriptSnapshot(resolvedFileName: string, fileContent: string) {
      // TODO: implement getChangeRange?
      return preciseScriptSnapshot(fileContent);
    }
  }

  export namespace codeBuilder {

    export type DefaultImport = {};
    export const DefaultImport: DefaultImport = {};

    export type StarImport = {};
    export const StarImport: StarImport = {};

    export type RawImport = {};
    export const RawImport: RawImport = {};

    const regex_DTS = /\.d\.ts$/i;
    const regex_MAP = /\.map$/i;

    const regex_getFileExt = /^.*[^.\\/](\.[^.\\/]+)$/;
    const regex_javascriptFileExtension = /\.(js|jsx)$/i;

    export function isTypingsFilename(fileName: string) { return regex_DTS.test(fileName); }

    export function isSourcemapFilename(fileName: string) { return regex_MAP.test(fileName); }

    export function getFileExtension(fileName: string) {
      const ext = fileName.replace(regex_getFileExt, '$1');
      if (ext === fileName) // failed to match the .ext regexp
        return '';
      else
        return ext;
    }

    export function hasJavascriptFileExtension(file: string) {
      return regex_javascriptFileExtension.test(file);
    }

  }

  function shouldSkipDirectoryContentFromIteration(fName: string) {
    return fName === 'node_modules'
      || fName === '.git'
      || fName === '.DS_Store';
  }

  function readDirectory(
    projectDir: string,
    fileSystem: FileSystem,
    rootDir: string,
    extensions: readonly string[],
    excludes: readonly string[] | undefined,
    includes: readonly string[],
    depth?: number | undefined): readonly string[] {

    let result: string[] = [];
    readDirectoryRecursive(
      fileSystem.resolvePath(projectDir, rootDir),
      depth);

    return result;

    function readDirectoryRecursive(resolvedDirPath: string, moreDepth: number | undefined): void {
      const dirEntries = fileSystem.readDirectory(resolvedDirPath);
      if (!dirEntries) return;

      const ownFiles: string[] = [];
      dirEntries.sort();
      for (const entry of dirEntries) {
        const stat = fileSystem.stat(entry);
        if (!stat) continue;
        if (stat.isDirectory) {
          readDirectoryRecursive(entry, typeof moreDepth === 'undefined' ? moreDepth : moreDepth - 1);
        }
        else if (!extensions || !extensions.length || extensions.indexOf(codeBuilder.getFileExtension(entry).toLowerCase()) >= 0) {
          // TODO: handle excludes/includes
          ownFiles.push(fileSystem.getRelativePath(rootDir, entry));
        }
      }

      if (ownFiles.length) {
        for (let i = 0; i < ownFiles.length; i++) {
          result.push(ownFiles[i]);
        }
      }
    }
  }

  function createImportOriginEntry(resolvedSourcePath: string, node: typescript.Node) {
    const sourceFile = node.getSourceFile();
    const start = sourceFile.getLineAndCharacterOfPosition(node.getStart());
    const lineStart = sourceFile.getPositionOfLineAndCharacter(start.line, 0);
    const lineEnd = sourceFile.getLineEndOfPosition(node.getStart());
    const lineText = sourceFile.getText().slice(lineStart, lineEnd);
    const nodeEndOrLineEnd = sourceFile.getLineAndCharacterOfPosition(Math.min(node.getEnd(), lineEnd));
    const importOrigin = {
      resolvedSourcePath,
      line: start.line,
      character: start.character,
      length: nodeEndOrLineEnd.character - start.character,
      lineText
    };
    return importOrigin;
  }

  function generatePlainEmitWithEmptyFile(sourceName: string, text: string) {
    const sourcemap: danfo.parsing.sourcemap.Sourcemap = {
      version: 3,
      file: sourceName,
      sourceRoot: '',
      sources: [sourceName],
      names: [],
      mappings: '',
      sourcesContent: [text]
    };

    const sourcemapText = JSON.stringify(sourcemap);

    return {
      resolvedSourcePath: sourceName,
      output: '\n//# sourceMappingURL=' + sourceName + '.map',
      sourcemap: sourcemapText
    };
  }

  function generateHelperEmitWithPerLineMaps(h: { name: string, text: string | ((node: ((nm: string) => string)) => string) }) {
    const sourceName = h.name + '.js';
    const sourcePath = '__helpers/' + sourceName;
    const sourceContent = typeof h.text === 'string' ? h.text : h.text((nm: string) => nm);
    const sourcemap: danfo.parsing.sourcemap.Sourcemap = {
      version: 3,
      file: sourceName,
      sourceRoot: '',
      sources: [sourcePath],
      names: [],
      mappings: '',
      sourcesContent: [sourceContent]
    };

    const sourcemapWriter = danfo.parsing.sourcemap.writer();
    let linePos = 0;
    let mapping: danfo.parsing.sourcemap.Mapping = {
      generatedLine: 0,
      generatedColumn: 0,
      originalLine: 0,
      originalColumn: 0,
      originalFile: 0,
      originalName: -1
    };
    while (linePos < sourceContent.length) {
      let lineEnd = sourceContent.indexOf('\n', linePos);
      if (lineEnd < 0) lineEnd = sourceContent.length;
      mapping.generatedLine++;
      mapping.originalLine++;
      sourcemapWriter.write(mapping);
      linePos = lineEnd + 1;
    }

    sourcemap.mappings = sourcemapWriter.text();
    const sourcemapText = JSON.stringify(sourcemap);
    const sourcemapFileName = sourceName + '.map';

    return {
      resolvedSourcePath: sourcePath,
      output: sourceContent + '\n//# sourceMappingURL=' + sourcemapFileName,
      sourcemap: { fileName: sourcemapFileName, content: sourcemapText }
    };
  }

}
