namespace danfo.build.code {

  export declare namespace findDependencies {

    type Dependency = {
      async: boolean;
      dynamic: boolean;
      importNode: typescript.Node;
      moduleNode: typescript.Node;
      assignsTo?: DependencyTarget;
      reference: string | string[] | null;
      sourceFile: typescript.SourceFile;
      resolvedSourceFile?: typescript.SourceFile;
    }

    type Export = {
      kind: 'exportStatement' | 'exportsAssignment' | 'amd';
      exportNode: typescript.Node;
      wholeModuleBody?: typescript.Node;
      moduleNameOverride?: string;
      defaultExport?: typescript.Node;
      namedExports?: {
        name: string;
        exportedAs: typescript.Node;
      }[];
    }

    type DependencyTarget = {
      defaultTarget?: typescript.Node;
      starTarget?: typescript.Node;
      named?: { sourceName: string, target?: typescript.Node }[];
    }
    
    type Input = {
      program: typescript.Program;
      resolutionHost: typescript.ModuleResolutionHost;
      resolutionCache: typescript.ModuleResolutionCache;
      ts: typeof import('typescript');
      sourceFile: typescript.SourceFile;
      transformationContext?: typescript.TransformationContext,
      dependencyCallback?: (dependency: findDependencies.Dependency) => typescript.Node | void | undefined;
      exportCallback?: (dependency: findDependencies.Export) => typescript.Node | void | undefined;
    }
    
    type Output = {
      transformed?: typescript.SourceFile;
      imports?: findDependencies.Dependency[];
      exports?: Export[];
    }
  }

  export function findDependencies(args: findDependencies.Input): findDependencies.Output {
    const {
      program,
      resolutionHost,
      resolutionCache,
      ts,
      sourceFile,
      transformationContext,
      exportCallback, dependencyCallback
    } = args;

    const dependencies: findDependencies.Output = {
      transformed: sourceFile
    };

    const checker = program.getTypeChecker();

    dependencies.transformed = invokeTransformationOrSimpleVisitor();

    return dependencies;

    function invokeTransformationOrSimpleVisitor() {
      let visitChildNodes: (node: typescript.Node, visitor: (node: typescript.Node) => typescript.Node | undefined) => typescript.Node | undefined;
      if (typeof exportCallback !== 'function' && typeof dependencyCallback !== 'function') {
        // use a simple visitor, without transforms
        visitChildNodes = (node, visitor) =>
          ts.forEachChild(node, visitor);

        visitDependencies(sourceFile) || sourceFile as any;
      }
      else if (transformationContext) {
        // transform with a given transformation context
        visitChildNodes = (node, visitor) =>
          ts.visitEachChild(node, visitor, transformationContext);

        return visitDependencies(sourceFile) || sourceFile as any;
      }
      else {
        // transform without a transformation context passed:
        // by invoking `ts.transform` and passing a manufactured custom transformer,
        // which will invoke the visitor we have
        const transformResult = ts.transform(sourceFile, [
          context => sourceFileNode => {
            visitChildNodes = (node, visitor) =>
              ts.visitEachChild(node, visitor, context);

            return visitDependencies(sourceFileNode) as typescript.SourceFile || sourceFile;
          }
        ], program.getCompilerOptions());

        transformResult.dispose();
        return transformResult.transformed[0];
      }
    }

    function visitDependencies(input: typescript.Node) {
      let processed: typescript.Node | undefined = ts.forEachChild<typescript.Node>(input, visitDependencies) || input;

      if (ts.isImportDeclaration(input)) {
        // console.log('import declaration ' + ts.SyntaxKind[input.kind] + ' at ' + input.pos);
        processed = includeImportDeclaration(input);
      }
      else if (ts.isImportEqualsDeclaration(input)) {
        // console.log('import equals declaration ' + ts.SyntaxKind[input.kind] + ' at ' + input.pos);
        // TODO: handle this weird old syntax
      }
      else if (isImportExpression(input, ts)) {
        // console.log('import expression ' + ts.SyntaxKind[input.kind] + ' at ' + input.pos);
        processed = includeImportExpression(input);
      }
      else if (isRequireExpression(input, ts, checker)) {
        // console.log('require declaration ' + ts.SyntaxKind[input.kind] + ' at ' + input.pos);
        processed = includeRequireDeclaration(input);
      }
      else if (ts.isExportAssignment(input)) {
        processed = inlcudeExportAssignment(input);
      }
      else if (ts.isExportDeclaration(input)) {
        processed = inlcudeExportDeclaration(input);
      }
      else if (isDeclarationWithModifier(input, ts.SyntaxKind.ExportKeyword, ts)) {
        processed = includeDeclarationWithExportModifier(input);
      }
      else if (isModuleExportsAssignment(input, ts, checker)) {
        processed = includeModuleExportsAssignment(input);
      }
      else if (isModuleExportsPropertyAssignment(input, ts, checker)) {
        processed = includeModuleExportsPropertyAssignment(input);
      }
      else {
        const amdDefine = isAmdDefineCall(input, ts, checker);
        if (amdDefine) {
          processed = includeAmdImportsExports(amdDefine);
        }
      }
      
      if (processed !== input)
        return processed;
    }


    function createDependency(
      import_: typescript.Node,
      moduleNode: typescript.Node,
      assignsTo: findDependencies.DependencyTarget | undefined,
      async: boolean) {
      const reference = getPossibleStringLiteralValues(moduleNode, ts, checker);

      let resolvedSourceFile: typescript.SourceFile | undefined;

      if (typeof reference === 'string') {
        const resolvedReference = ts.resolveModuleName(
          reference,
          import_.getSourceFile().fileName,
          program.getCompilerOptions(),
          resolutionHost,
          resolutionCache);

        if (resolvedReference.resolvedModule
          && resolvedReference.resolvedModule.resolvedFileName) {
          resolvedSourceFile = program.getSourceFile(resolvedReference.resolvedModule.resolvedFileName);
        }
      }
      
      const dep = {
        async,
        dynamic: typeof reference !== 'string',
        importNode: import_,
        moduleNode,
        assignsTo,
        reference,
        sourceFile: sourceFile,
        resolvedSourceFile
      };
        
      if (!dependencies.imports)
        dependencies.imports = [dep];
      else
        dependencies.imports.push(dep);
      
      if (typeof dependencyCallback === 'function') {
        const converted = dependencyCallback(dep);
        if (converted) {
          return converted;
        }
      }
    }

    function includeImportDeclaration(node: typescript.ImportDeclaration) {
      return createDependency(
        node,
        node.moduleSpecifier,
        getImportDeclarationAssigns(node),
        /* async */ false);
    }

    function getImportDeclarationAssigns(node: typescript.ImportDeclaration): findDependencies.DependencyTarget | undefined {
      let assignsTo: findDependencies.DependencyTarget | undefined;
      if (node.importClause) {
        if (node.importClause.name) {
          assignsTo = {
            defaultTarget: node.importClause.name
          };
        }

        if (node.importClause.namedBindings) {
          if (ts.isNamedImports(node.importClause.namedBindings)) {
            const named: { sourceName: string, target: typescript.Node }[] = [];
            for (const bin of node.importClause.namedBindings.elements) {
              named.push({
                sourceName: bin.name.text,
                target: bin.propertyName || bin.name
              });
            }

            if (!assignsTo)
              assignsTo = { named };
            else
              assignsTo.named = named;
          }
          else if (ts.isNamespaceImport(node.importClause.namedBindings)) {
            if (!assignsTo)
              assignsTo = {
                starTarget: node.importClause.namedBindings.name
              };
            else
              assignsTo.starTarget = node.importClause.namedBindings.name;
          }
        }
      }
      return assignsTo;
    }

    function includeImportExpression(node: typescript.CallExpression) {
      return createDependency(
        node,
        node.arguments[0],
        getExpressionAssigns(node),
        /* async */ true);
    }

    function includeRequireDeclaration(node: typescript.CallExpression) {
      return createDependency(
        node,
        node.arguments[0],
        getExpressionAssigns(node),
        /* async */ false);
    }

    function getExpressionAssigns(node: typescript.Expression): findDependencies.DependencyTarget | undefined {
      if (ts.isVariableDeclaration(node.parent) && node.parent.initializer === node) {
        return { starTarget: node.parent.name };
      }

      // TODO: detect other expressions
    }

    function inlcudeExportAssignment(node: typescript.ExportAssignment) {

      return createExport({
        exportNode: node,
        kind: 'exportsAssignment',
        defaultExport: node
      });
    }

    function inlcudeExportDeclaration(node: typescript.ExportDeclaration) {
      if (node.exportClause) {
        if (ts.isNamespaceExport(node.exportClause)) {
          const name = getPossibleNameString(node.exportClause.name, ts);
          if (typeof name === 'string') {
            return createExport({
              exportNode: node,
              kind: 'exportStatement',
              namedExports: [{
                name: name,
                exportedAs: node
              }]
            });
          }
        }
        else {
          const namedExports = [] as {
            name: string;
            exportedAs: typescript.Node;
          }[];

          for (const el of node.exportClause.elements) {
            const name = getPossibleNameString(el.name, ts);
            if (typeof name === 'string')
              namedExports.push({
                name,
                exportedAs: el
              });
          }

          return createExport({
            exportNode: node,
            kind: 'exportStatement',
            namedExports
          });
        }
      }
    }

    function includeDeclarationWithExportModifier(node: typescript.Node) {
      const isDefault = isDeclarationWithModifier(node, ts.SyntaxKind.DefaultKeyword, ts);
      const nameNode = isDefault ? void 0 : (node as typescript.FunctionDeclaration).name;
      const name = nameNode && getPossibleNameString(nameNode, ts);
      return createExport({
        exportNode: node,
        kind: 'exportStatement',
        defaultExport: isDefault ? node : void 0,
        namedExports: typeof name === 'string' ? [{
          name,
          exportedAs: node
        }] : void 0
      });
    }
    
    function includeModuleExportsAssignment(node: typescript.PropertyAssignment) {
      const name = getPossibleNameString(node.name, ts);
      if (typeof name === 'string')
        return createExport({
          exportNode: node,
          wholeModuleBody: node,
          kind: 'exportsAssignment',
          namedExports: [{
            name,
            exportedAs: node.initializer
          }]
        });
    }
    
    function includeModuleExportsPropertyAssignment(node: typescript.PropertyAssignment) {
      const name = getPossibleNameString(node.name, ts);
      if (typeof name === 'string')
        return createExport({
          exportNode: node,
          kind: 'exportsAssignment',
          namedExports: [{
            name,
            exportedAs: node.initializer
          }]
        });
    }

    function includeAmdImportsExports(amdDefine: AmdDefineCall) {
      if (amdDefine.moduleName) {
        createExport({
          exportNode: amdDefine.node,
          wholeModuleBody: amdDefine.moduleBodyArgument,
          kind: 'amd',
          moduleNameOverride: amdDefine.moduleName
        });
      }

      if (amdDefine.imports && amdDefine.imports.length) {
        createDependency(
          amdDefine.moduleBodyArgument.parent,
          amdDefine.moduleBodyArgument,
          {
            named: amdDefine.imports
          },
          false);
      }
      return void 0;
    }

    function createExport(
      exportElement: findDependencies.Export) {
      if (!dependencies.exports)
        dependencies.exports = [exportElement];
      else
        dependencies.exports.push(exportElement);

      if (typeof exportCallback === 'function') {
        const converted = exportCallback(exportElement);
        if (converted) {
          return converted;
        }
      }
    }

  }

}