namespace danfo.build.bundle {

  export function codeBundler(options: codeBundler.Options): Bundler {

    const fileSystem = options.fileSystem;

    const codeBuilder = code.codeBuilder({
      fileSystem,
      precachedFixedSource: options.precachedFixedSource
    });

    return {
      build: runBuild
    };

    function runBuild(inputs: BundleInputs): BundleResult {

      const resolvedProjectDirWithSlash = inputs.resolvedProjectDir.charCodeAt(inputs.resolvedProjectDir.length - 1) === CharCodes.Slash ? inputs.resolvedProjectDir : inputs.resolvedProjectDir + '/';
      let outputFile = inputs.outputFile;

      const buildResults = codeBuilder.build({
        resolvedProjectDir: inputs.resolvedProjectDir,
        sources: inputs.sources,
        handleSettings
      });

      if (!outputFile) {
        if (inputs.sources.length === 1)
          outputFile = resolvedProjectDirWithSlash + 'dist/' + getBaseName(inputs.sources[0].resolvedPath);
        else
          outputFile = resolvedProjectDirWithSlash + 'dist/index.js';
      }
      else {
        outputFile = fileSystem.resolvePath(resolvedProjectDirWithSlash, outputFile);
        if (fileSystem.stat(outputFile)?.isDirectory)
          outputFile = outputFile + '/index.js';
      }

      const concatenated = danfo.parsing.sourcemap.concatenate(
        { fileName: getBaseName(outputFile) },
        buildResults.emits.map(em => {
          return {
            ...em,
            resolvePath: path => path.replace(/\\/g, '')
          };
        }));
      
      return {
        outputFile,
        output: typeof concatenated === 'string' ? concatenated : concatenated.output,
        sourcemap: typeof concatenated === 'string' ? void 0 : concatenated.sourcemap,
        errors: buildResults.errors,
        imports: buildResults.imports
      };

      function handleSettings(settings: typescript.CompilerOptions): typescript.CompilerOptions {
        if (typeof outputFile === 'undefined') {
          if (settings.out || settings.outFile) {
            outputFile = settings.out || settings.outFile;
          }
          else if (settings.outDir) {
            outputFile = fileSystem.resolvePath(resolvedProjectDirWithSlash, settings.outDir + '/index.js');
          }
        }

        if (inputs.outputDir && !settings.outDir) {
          return {
            ...settings,
            outFile: void 0,
            out: void 0,
            outDir: inputs.outputDir
          };
        }
        else {
          return settings;
        }
      }

    }
  }

  export namespace codeBundler {

    const regex_typeScriptFileExtension = /\.(ts|tsx)$/i;
    const regex_jsonFileExtension = /\.json$/i;

    export function hasTypeScriptFileExtension(file: string) {
      return regex_typeScriptFileExtension.test(file);
    }

    export function hasJsonFileExtension(file: string) {
      return regex_jsonFileExtension.test(file);
    }

  }
}