namespace danfo.build.code {

  export type AmdDefineCall = {
    node: typescript.CallExpression;
    moduleName?: string;
    imports?: { sourceName: string, target?: typescript.Node }[];
    moduleBodyArgument: typescript.Node;
  }

  export function isAmdDefineCall(node: typescript.Node, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker): AmdDefineCall | undefined {
    const typeChecker = typeof (checker as typescript.Program).getTypeChecker === 'function' ?
      (checker as typescript.Program).getTypeChecker() :
      (checker as typescript.TypeChecker);

    if (ts.isCallExpression(node)
      && isGlobalName(node.expression, 'define', ts, typeChecker)
      && node.arguments.length >= 2 && node.arguments.length <= 3) {
      const factoryArgument = node.arguments[node.arguments.length - 1];
      const factorySignature = getPossibleFunctionArguments(factoryArgument, ts, typeChecker);
      if (factorySignature) {
        if (!factorySignature)
          return void 0; // function should be the last parameter to define(...)

        if (node.arguments.length === 2) {
          const firstArgumentStringValues = getPossibleStringLiteralValues(node.arguments[0], ts, typeChecker);
          if (firstArgumentStringValues) {
            if (typeof firstArgumentStringValues === 'string')
              return {
                node,
                moduleName: firstArgumentStringValues,
                imports: [],
                moduleBodyArgument: factoryArgument
              };
            else
              return void 0; // if first argument is of string type, its value should be statically known
          }

          const firstArgumentArrayValues = getPossibleStringArrayValues(node.arguments[0], ts, typeChecker);
          if (firstArgumentArrayValues) {
            const imports: { sourceName: string, target?: typescript.Node }[] = [];
            for (let i = 0; i < firstArgumentArrayValues.length; i++) {

              imports.push({
                sourceName: firstArgumentArrayValues[i],
                target: i < factorySignature.length && (factorySignature[i] as typescript.Node).kind ?
                  factorySignature[i] as typescript.Node :
                  void 0
              });
            }

            if (imports.length) {
              return {
                node,
                imports,
                moduleBodyArgument: factoryArgument
              };
            }
          }
        }
        else if (node.arguments.length === 3) {
          const firstArgumentStringValues = getPossibleStringLiteralValues(node.arguments[0], ts, typeChecker);
          if (typeof firstArgumentStringValues === 'string') {

            const secondArgumentArrayValues = getPossibleStringArrayValues(node.arguments[1], ts, typeChecker);
            if (secondArgumentArrayValues) {
              const imports: { sourceName: string, target?: typescript.Node }[] = [];
              for (let i = 0; i < secondArgumentArrayValues.length; i++) {
                imports.push({
                  sourceName: secondArgumentArrayValues[i],
                  target: i < factorySignature.length && (factorySignature[i] as typescript.Node).kind ?
                    factorySignature[i] as typescript.Node :
                    void 0
                });
              }

              return {
                node,
                moduleName: firstArgumentStringValues,
                imports: imports.length ? imports : void 0,
                moduleBodyArgument: factoryArgument
              };
            }
          }
        }
      }
    }

    return void 0;
  }
  
  export function isGlobalName(node: typescript.Node, name: string, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker): boolean {
    const typeChecker = typeof (checker as typescript.Program).getTypeChecker === 'function' ?
      (checker as typescript.Program).getTypeChecker() :
      (checker as typescript.TypeChecker);

    return isGlobalNameWorker(node, name, [], ts, typeChecker);
  }

  function isGlobalNameWorker(node: typescript.Node, name: string, visitedNodes: typescript.Node[], ts: typeof import('typescript'), checker: typescript.TypeChecker): boolean {
    const fnSymbol = checker.getSymbolAtLocation(node);
    if (!fnSymbol)
      return ts.isIdentifier(node) && node.text === name;

    const declArray = fnSymbol.getDeclarations();
    if (!declArray || !declArray.length)
      return fnSymbol.name === name;

    for (const decl of declArray) {
      if (ts.isVariableDeclaration(decl)) {
        if (decl.initializer) {
          return invokeRecursivePredicateEscapeCycles(
            isGlobalNameWorker,
            decl.initializer,
            name,
            visitedNodes,
            ts, checker);
        }

        // check if global scope?
        if (isVariableGlobalScope(decl, ts) && ts.isIdentifier(decl.name)) {
          return decl.name.text === name;
        }
        // console.log('variable decl: ', ts.SyntaxKind[decl.kind], decl);
        return false;
      }
      else if (ts.isParameter(decl)) {
        // parameter can be defaulted to global symbol - check
        if (decl.initializer) {
          if (invokeRecursivePredicateEscapeCycles(
            isGlobalNameWorker,
            decl.initializer,
            name,
            visitedNodes,
            ts, checker))
            return true;
        }

        // or it could be IIFE, check arguments then
        const fnExpr = decl.parent;
        if (ts.isFunctionExpression(fnExpr)) {
          const fnInvok = getIIFEInvocation(fnExpr, ts);
          if (fnInvok) {
            const paramIndex = fnExpr.parameters.indexOf(decl);
            const invokParam = fnInvok.arguments[paramIndex];
            if (invokParam) {
              return invokeRecursivePredicateEscapeCycles(
                isGlobalNameWorker,
                invokParam,
                name,
                visitedNodes,
                ts, checker);
            }
          }
        }
        return false;
      }
    }

    return false;
  }

  function invokeRecursivePredicateEscapeCycles<T, Arg, TState1, TState2>(
    predicate: (item: T, arg: Arg, visited: T[], s1: TState1, s2: TState2) => boolean,
    item: T,
    arg: Arg,
    visited: undefined | T[],
    s1: TState1, s2: TState2): boolean {

    if (visited) {
      if (visited.indexOf(item))
        return false;
      visited.push(item);
    }
    else {
      visited = [item];
    }

    return predicate(item, arg, visited, s1, s2);
  }


  export function isVariableGlobalScope(variable: typescript.VariableDeclaration, ts: typeof import('typescript')) {
    return ts.isSourceFile(variable.parent.parent.parent);
  }

  export function getIIFEInvocation(fnExpr: typescript.Node, ts: typeof import('typescript')): typescript.CallExpression | undefined {
    let fnCurrent = fnExpr;
    for (let i = 0; fnCurrent.parent && i < 5; i++) { // max check depth
      if (ts.isCallExpression(fnCurrent.parent) && fnCurrent.parent.expression === fnCurrent)
        return fnCurrent.parent;

      if (!ts.isParenthesizedExpression(fnCurrent.parent))
        return;

      fnCurrent = fnCurrent.parent;
    }
  }

  export function isDeclarationWithModifier(node: typescript.Node, modifier: typescript.SyntaxKind, ts: typeof import('typescript')): boolean {
    const withModifiers = node as typescript.HasModifiers;
    if (withModifiers.modifiers) {
      for (const mod of withModifiers.modifiers) {
        if (mod.kind === modifier)
          return true;
      }
    }

    return false;
  }

  /** module.exports = ... */
  export function isModuleExportsAssignment(node: typescript.Node, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker): node is typescript.PropertyAssignment {
    if (ts.isPropertyAssignment(node)
      && ts.isIdentifier(node.parent)
      && isGlobalName(node.parent, 'module', ts, checker)
      && getPossibleNameString(node.name, ts) === 'exports') {
      return true;
    }

    return false;

  }

  /** module.exports.xxxxx = ... */
  export function isModuleExportsPropertyAssignment(node: typescript.Node, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker): node is typescript.PropertyAssignment {
    if (ts.isBinaryExpression(node)
      && node.operatorToken.kind === ts.SyntaxKind.EqualsToken
      && ts.isPropertyAccessExpression(node.parent)
      && node.parent.parent
      && ts.isIdentifier(node.parent.parent)
      && isGlobalName(node.parent.parent, 'module', ts, checker)
      && getPossibleNameString(node.parent.name, ts) === 'export') {
      return true;
    }

    return false;
  }

  export function getPossibleNameString(node: typescript.Node, ts: typeof import('typescript')): string | undefined {
    if (ts.isIdentifier(node))
      return node.text;

    if (ts.isStringLiteralLike(node)) // TODO: handle escapes?
      return node.text;

    if (ts.isNumericLiteral(node)) // TODO: normalize?
      return node.text;
  }

  export function isRequireExpression(node: typescript.Node, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker): node is typescript.CallExpression {
    return ts.isCallExpression(node) && isGlobalName(node.expression, 'require', ts, checker);
  }

  export function isImportExpression(node: typescript.Node, ts: typeof import('typescript')): node is typescript.CallExpression {
    return ts.isCallExpression(node) && node.expression.kind === ts.SyntaxKind.ImportKeyword;
  }

  export function getPossibleFunctionArguments(node: typescript.Node, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker) {
    if (ts.isFunctionExpression(node))
      return node.parameters;
    
    const typeChecker = typeof (checker as typescript.Program).getTypeChecker === 'function' ?
      (checker as typescript.Program).getTypeChecker() :
      (checker as typescript.TypeChecker);

    const expressionType = typeChecker.getTypeAtLocation(node);
    const callSignatures = expressionType.getCallSignatures();
    let longestSignature = callSignatures[0];
    for (let i = 1; i < callSignatures.length; i++) {
      if (callSignatures[i].parameters.length > longestSignature.parameters.length)
        longestSignature = callSignatures[i];
    }
    return longestSignature ? longestSignature.parameters : null;
  }

  export function getPossibleStringLiteralValues(expression: typescript.Node, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker): string | string[] | null {
    if (ts.isStringLiteralLike(expression))
      return expression.text;

    const typeChecker = typeof (checker as typescript.Program).getTypeChecker === 'function' ?
      (checker as typescript.Program).getTypeChecker() :
      (checker as typescript.TypeChecker);

    const expressionType = typeChecker.getTypeAtLocation(expression);
    return getPossibleStringLiteralValuesOfType(expressionType);
  }

  export function getPossibleStringLiteralValuesOfType(type: typescript.Type): string | string[] | null {
    if (type.isStringLiteral()) {
      return type.value;
    }

    if (type.isUnion()) {
      let unionTypeValues: string[] = [];
      let hasNull = false;
      for (const uniType of type.types) {
        const values = getPossibleStringLiteralValuesOfType(uniType);
        if (typeof values === 'string')
          unionTypeValues.push(values);
        else if (values && values.length)
          unionTypeValues = unionTypeValues.concat(values);
      }
      return !unionTypeValues.length ? null : unionTypeValues.length === 1 ? unionTypeValues[0] : unionTypeValues;
    }

    return null;
  }

  export function getPossibleStringArrayValues(node: typescript.Node, ts: typeof import('typescript'), checker: typescript.Program | typescript.TypeChecker) {
    const typeChecker = typeof (checker as typescript.Program).getTypeChecker === 'function' ?
      (checker as typescript.Program).getTypeChecker() :
      (checker as typescript.TypeChecker);

    if (ts.isArrayLiteralExpression(node)) {
      const strings: string[] = [];
      for (const el of node.elements) {
        const elString = getPossibleStringLiteralValues(el, ts, checker);
        if (typeof elString === 'string')
          strings.push(elString);
        else
          break;
      }

      return strings;
    }
  }


}