namespace danfo.build {

  export declare namespace populateSourceStructure {
    type Options = {
      fileSystem: FileSystem;
      sources: string[];
      projectPath?: string;
      exclude?: (resolvedPath: string, relativePath: string, stat: FileSystem.Stat | undefined) => boolean;
    }

    type Result = {
      /** in certain error cases may remain undefined */
      projectDir: string | undefined;
      sources: SourceFileOrSpan[];
      missing?: SourceFileOrSpan[];
    }
  }

  type TopLevelEntry = {
    originalSourcePath: string;
    resolvedPath: string;
    type: 'dir' | 'file' | 'missing' | 'unresolved-proj-relative';
    directoryPath?: string;
    stat?: FileSystem.Stat
  }

  type addTopLevelEntry = (topLevelEntry: TopLevelEntry) => void;

  export function populateSourceStructure(options: populateSourceStructure.Options): populateSourceStructure.Result {
    const topLevelEntryList: TopLevelEntry[] = [];
    const topLevelEntriesByResolvedPath: { [resolvedPath: string]: TopLevelEntry } = {};

    let { projectDir, projectTopLevelEntry } = verifyProjectPath(options.fileSystem, options.projectPath);
    if (projectTopLevelEntry) topLevelEntryList.push(projectTopLevelEntry);

    populateTopLevelEntries(options.fileSystem, projectDir, options.sources, addTopLevelEntry);

    let rootDirs: string[] = [];
    for (const entry of topLevelEntryList) {
      if (entry.directoryPath) {
        rootDirs.push(entry.directoryPath);
      }
    }
    
    if (!projectDir) {
      projectDir = resolveProjectDirFromRootDirs(rootDirs);

      if (projectDir)
        lateResolveProjRelatives(options.fileSystem, projectDir, topLevelEntryList);
    }

    let missing: SourceFileOrSpan[] | undefined;
    let sourceList: (SourceFileOrSpan | { type: 'root-dir', sourceFileEntries: SourceFileOrSpan[] })[] = [];
    let topLevelHTML = false;
    let topLevelTsconfig = false;
    let topLevelPackage = false;

    for (const topLevelEntry of topLevelEntryList) {
      if (topLevelEntry.type === 'missing' || topLevelEntry.type === 'unresolved-proj-relative') {
        const missingEntry = {
          resolvedPath: topLevelEntry.resolvedPath,
          originalSourcePath: topLevelEntry.originalSourcePath
        };
        if (!missing) missing = [missingEntry];
        else missing.push(missingEntry);
      }
      else if (topLevelEntry.type === 'file') {
        const type = populateSourceStructure.inferSourceFileTypeFromExtension(topLevelEntry.originalSourcePath);
        sourceList.push({
          resolvedPath: topLevelEntry.resolvedPath,
          originalSourcePath: topLevelEntry.originalSourcePath,
          type,
          stat: topLevelEntry.stat
        });
        if (type === 'html') topLevelHTML = true;
        else if (type === 'tsconfig') topLevelTsconfig = true;
        else if (type === 'package') topLevelPackage = true;
      }
      else if (topLevelEntry.type === 'dir') {
        const sourceFileEntries: SourceFileOrSpan[] = [];
        populateDir(
          topLevelEntry.resolvedPath,
          rootDirs.length === 1 ? DirType.OnlyRoot : DirType.Root,
          projectDir || topLevelEntry.resolvedPath /* baseDirPath */,
          sourceFileEntries);

        sourceList.push({
          type: 'root-dir',
          sourceFileEntries
        });
      }
    }

    const result: populateSourceStructure.Result = {
      projectDir,
      sources: [],
      missing
    };

    for (const sourceEntry of sourceList) {
      if (sourceEntry.type === 'root-dir') {
        for (const sourceFileEntry of sourceEntry.sourceFileEntries) {
          if (sourceFileEntry.type === 'html') {
            if (topLevelHTML) continue;
            topLevelHTML = true;
          }
          else if (sourceFileEntry.type === 'tsconfig') {
            if (topLevelTsconfig) continue;
            topLevelTsconfig = true;
          }
          else if (sourceFileEntry.type === 'package') {
            if (topLevelPackage) continue;
            topLevelPackage = true;
          }

          result.sources.push(sourceFileEntry);
        }
      }
      else {
        result.sources.push(sourceEntry);
      }
    }

    return result;

    function addTopLevelEntry(topLevelEntry: TopLevelEntry) {
      let existingTopLevelEntry = topLevelEntriesByResolvedPath[topLevelEntry.resolvedPath];
      if (existingTopLevelEntry) {
        if (!existingTopLevelEntry.originalSourcePath && topLevelEntry.originalSourcePath) existingTopLevelEntry.originalSourcePath = topLevelEntry.originalSourcePath;
        if (!existingTopLevelEntry.directoryPath && topLevelEntry.directoryPath) existingTopLevelEntry.directoryPath = topLevelEntry.directoryPath;
      }
      else {
        topLevelEntriesByResolvedPath[topLevelEntry.resolvedPath] = topLevelEntry;
        topLevelEntryList.push(topLevelEntry);
      }
    }

    const enum DirType {
      Any,
      Root,
      OnlyRoot
    }

    function populateDir(dirPath: string, dirType: DirType, baseDirPath: string, populateSourceList: SourceFileOrSpan[]) {
      const dirFileList = options.fileSystem.readDirectory(dirPath);
      if (!dirFileList) return;
      dirFileList.sort();

      let subdirectoryList: { resolvedPath: string, stat: FileSystem.Stat }[] | undefined;
      const fileSourceList: SourceFileOrSpan[] = [];
      let indexHTMLs: SourceFileOrSpan[] | undefined;
      let otherCandidateHTMLs: SourceFileOrSpan[] | undefined;
      let tsconfigs: SourceFileOrSpan[] | undefined;
      let packages: SourceFileOrSpan[] | undefined;

      for (const file of dirFileList) {
        const resolvedPath = options.fileSystem.resolvePath(dirPath, file);
        const baseName = getBaseName(resolvedPath);

        // don't even touch those .git entries, no point creating watch entries on them
        if (regex_excludeDirectory.test(baseName)) continue;

        const stat = options.fileSystem.stat(resolvedPath);
        if (!stat) continue;
        if (stat.isDirectory) {
          if (!subdirectoryList) subdirectoryList = [{ resolvedPath, stat }];
          else subdirectoryList.push({ resolvedPath, stat });
          continue;
        }

        const type = populateSourceStructure.inferSourceFileTypeFromExtension(resolvedPath);
        if (dirType === DirType.Root || dirType === DirType.OnlyRoot) {
          if (type === 'html' || type === 'tsconfig' || type === 'package') {
            // look out for project-level 'root' index.html and tsconfig.json
            let source: SourceFileOrSpan = {
              resolvedPath,
              type,
              stat
            };
            fileSourceList.push(source);

            if (type === 'html') {
              const baseName = getBaseName(resolvedPath);
              if (regex_fileName_indexHTML.test(baseName)) {
                if (!indexHTMLs) indexHTMLs = [source];
                else indexHTMLs.push(source);
              }
              else if (regex_fileName_candidateHTML.test(baseName)) {
                if (!otherCandidateHTMLs) otherCandidateHTMLs = [source];
                else otherCandidateHTMLs.push(source);
              }
            }
            else if (type === 'tsconfig') {
              if (!tsconfigs) tsconfigs = [source];
              else tsconfigs.push(source);
            }
            else if (type === 'package') {
              if (!packages) packages = [source];
              else packages.push(source);
            }
            continue;
          }
        }

        if (type === 'javascript' || type === 'typescript') {
          fileSourceList.push({
            resolvedPath,
            type,
            stat
          });
        }
      }

      if (dirType === DirType.OnlyRoot) {
        let codeFileCount = 0;
        for (const f of fileSourceList) {
          if (f.type === 'typescript' || f.type === 'javascript') {
            codeFileCount++;
          }
          else if (f.type === 'html') {
            const htmlCandidateBaseName = getBaseName(f.resolvedPath);
            if (regex_fileName_indexHTML.test(htmlCandidateBaseName) || regex_fileName_candidateHTML.test(htmlCandidateBaseName))
              codeFileCount++;
          }
        }
        if (!codeFileCount && subdirectoryList?.length) {
          // if the root dir has no source files, and has 'src' or 'sources' directory,
          // only search that directory and ignore other sibling subdirectories
          //
          // [myproj]
          //    [data] ... ignore ...
          //    [src]  ... YES, COMPILE THIS
          //    [test] ... ignore ...
          //
          const sourcesDirs: string[] = [];
          for (const subdir of subdirectoryList) {
            if (regex_dirName_src.test(getBaseName(subdir.resolvedPath))) sourcesDirs.push(subdir.resolvedPath);
          }

          if (sourcesDirs.length === 1) {
            populateDir(sourcesDirs[0], DirType.Root, baseDirPath, populateSourceList);
            return;
          }
        }
      }

      if (subdirectoryList) {
        for (const subdir of subdirectoryList) {
          const { exclude } = options;
          if (typeof exclude === 'function') {
            const relativeDirectoryPath = options.fileSystem.getRelativePath(baseDirPath, subdir.resolvedPath);
            if (exclude(subdir.resolvedPath, relativeDirectoryPath, subdir.stat)) continue;
          }

          populateDir(subdir.resolvedPath, DirType.Any, baseDirPath, populateSourceList);
        }
      }

      for (const file of fileSourceList) {
        if (file.type === 'html') {
          const includeAsHTML =
            (indexHTMLs?.length === 1 && indexHTMLs[0] === file)
            || (!indexHTMLs && otherCandidateHTMLs?.length === 1 && otherCandidateHTMLs[0] === file);
          
          if (!includeAsHTML) continue;
        }
        else if (file.type === 'tsconfig') {
          const includeAsTsconfig = tsconfigs?.length === 1;
          if (!includeAsTsconfig) continue;
        }
        else if (file.type === 'package') {
          const includeAsPackage = packages?.length === 1;
          if (!includeAsPackage) continue;
        }

        const { exclude } = options;
        if (typeof exclude === 'function') {
          const relativePath = projectDir ? options.fileSystem.getRelativePath(projectDir, file.resolvedPath) : file.resolvedPath;
          if (exclude(file.resolvedPath, relativePath, file.stat)) continue;
        }

        populateSourceList.push(file);
      }
    }
  }

  function lateResolveProjRelatives(fileSystem: FileSystem, projectDir: string, topLevelEntries: TopLevelEntry[]) {
    for (const entry of topLevelEntries) {
      if (entry.type === 'unresolved-proj-relative') {
        entry.resolvedPath = fileSystem.resolvePath(projectDir, entry.resolvedPath);
        entry.stat = fileSystem.stat(entry.resolvedPath);
        if (!entry.stat) {
          entry.type = 'missing';
        }
        else {
          if (entry.stat.isDirectory) {
            entry.type = 'dir';
            entry.directoryPath = entry.resolvedPath;
          }
          else {
            entry.type = 'file';
            entry.directoryPath = fileSystem.resolvePath(entry.resolvedPath, '..');
          }
        }
      }
    }
  }

  function verifyProjectPath(fileSystem: FileSystem, projectPath: string | undefined) {
    let projectDir = projectPath;
    let projectTopLevelEntry: TopLevelEntry | undefined;
    if (projectDir) {
      const originalSourcePath = projectDir;
      const resolvedPath = fileSystem.resolvePath(projectDir);
      const stat = fileSystem.stat(projectDir);
      if (!stat) {
        projectDir = void 0;
        projectTopLevelEntry = {
          originalSourcePath,
          resolvedPath,
          type: 'missing'
        };
      }
      else {
        if (stat.isDirectory) {
          projectTopLevelEntry = {
            originalSourcePath,
            resolvedPath,
            type: 'dir',
            directoryPath: resolvedPath,
            stat
          };
        }
        else {
          projectDir = fileSystem.resolvePath(projectDir, '..');
          projectTopLevelEntry = {
            originalSourcePath,
            resolvedPath,
            type: 'file',
            directoryPath: projectDir,
            stat
          };
        }
      }
    }

    return { projectDir, projectTopLevelEntry };
  }

  function populateTopLevelEntries(fileSystem: FileSystem, projectDir: string | undefined, sources: string[], addTopLevelEntry: addTopLevelEntry) {
    for (const originalSourcePath of sources) {
      let resolvedPath: string;
      const stripLeadingAtSlash = originalSourcePath.replace(regex_stripLeadingAtSlash, '');
      if (stripLeadingAtSlash !== originalSourcePath) {
        if (!projectDir) {
          addTopLevelEntry({
            originalSourcePath,
            resolvedPath: stripLeadingAtSlash,
            type: 'unresolved-proj-relative'
          });
          continue;
        }

        resolvedPath = fileSystem.resolvePath(projectDir, stripLeadingAtSlash);
      }
      else {
        resolvedPath = fileSystem.resolvePath(originalSourcePath);
      }

      const stat = fileSystem.stat(resolvedPath);

      if (!stat) {
        addTopLevelEntry({
          originalSourcePath,
          resolvedPath,
          type: 'missing'
        });
        continue;
      }

      if (stat.isDirectory) {
        addTopLevelEntry({
          originalSourcePath,
          resolvedPath,
          type: 'dir',
          directoryPath: resolvedPath,
          stat
        });
      }
      else {
        const directoryPath = fileSystem.resolvePath(resolvedPath, '..');
        addTopLevelEntry({
          originalSourcePath,
          resolvedPath,
          type: 'file',
          directoryPath,
          stat
        });
      }
    }
  }


  export namespace populateSourceStructure {

    export function inferSourceFileTypeFromExtension(path: string) {
      const extMatch = regex_ext.exec(path);
      let ext = extMatch && extMatch[1] || '';
      const type = inferSourceFileTypeFromExtensionLowercase(ext) || inferSourceFileTypeFromExtensionLowercase(ext.toLowerCase());
      if (type === 'json') {
        const baseName = getBaseName(path);
        if (regex_fileName_tsconfigJSON.test(baseName))
          return 'tsconfig';
        else if (regex_fileName_packageJSON.test(baseName))
          return 'package';
      }
      return type;
    }

    function inferSourceFileTypeFromExtensionLowercase(ext: string) {
      switch (ext) {
        case 'js':
        case 'jsx':
          return 'javascript';

        case 'ts':
        case 'tsx':
          return 'typescript';
        
        case 'css':
          return 'css';
        
        case 'html':
        case 'htm':
          return 'html';
        
        case 'json':
          return 'json';
      }
    }
  }

  function resolveProjectDirFromRootDirs(rootDirs: string[]): string | undefined {
    if (!rootDirs.length) return;
    return rootDirs[0];
  }

  const regex_ext = /\.([^\.\\\/]+)$/;
  const regex_fileName_indexHTML = /^index\.htm(l?)$/i;
  const regex_fileName_candidateHTML = /^default.htm(l?)$/i;
  const regex_fileName_tsconfigJSON = /^tsconfig\.json$/i;
  const regex_fileName_packageJSON = /^package\.json$/i;

  const regex_excludeDirectory = /(^|\\|\/|\:)(node_modules|\.git|\.DS_Store)$/i;
  const regex_dirName_src = /^(src|source|sources)$/i;

  const regex_stripLeadingAtSlash = /^\@[\\\/]+/;

}