namespace danfo.parsing.sourcemap {
  
  export type reader = Mapping & { read(): boolean };

  export function reader(mappings: string) {

    const numBuffer = [0, 0, 0, 0, 0, 0, 0, 0];

    let decodeSpanStart = 0;
    let pos = 0;
    const current: Mapping = {
      generatedLine: 0,
      generatedColumn: 0,
      originalLine: 0,
      originalColumn: 0,
      originalFile: 0,
      originalName: -1
    };

    const result: Mapping & { read(): boolean } = {
      generatedLine: 0,
      generatedColumn: 0,
      originalLine: 0,
      originalColumn: 0,
      originalFile: 0,
      originalName: -1,
      read
    };

    return result;

    function read(): boolean {

      for (; pos < mappings.length; pos++) {
        const ch = mappings.charCodeAt(pos);
        let parsedSpan = false;

        if (ch === CharCodes.Semicolon) {
          // new line indicator

          // keep reading if semicolon starts text, or follows another semicolon or colon
          if (pos > decodeSpanStart) {
            decodeCopyVerify();
            parsedSpan = true;
          }

          current.generatedLine++;
          current.generatedColumn = 0;
          decodeSpanStart = pos + 1;
        }
        else if (ch === CharCodes.Comma) {
          // keep reading if colon starts text, or follows another semicolon or colon
          if (pos > decodeSpanStart) {
            decodeCopyVerify();
            parsedSpan = true;
          }

          decodeSpanStart = pos + 1;
        }

        if (parsedSpan) {
          pos++;
          return true;
        }
      }

      // end of text, check if a span is being read
      if (pos > decodeSpanStart) {
        decodeCopyVerify();
        decodeSpanStart = pos + 1; // to terminate
        return true;
      }
      else {
        return false;
      }
    }

    function decodeCopyVerify() {
      decodeForMapping(mappings, decodeSpanStart, pos, numBuffer, current);
      copyMapping(current, result);
      verifyMapping(mappings, decodeSpanStart, pos, current);
    }

  }

  function verifyMapping(mappings: string, start: number, end: number, mappingEntry: Mapping) {
    if (mappingEntry.generatedColumn < 0
      || mappingEntry.originalLine < 0
      || mappingEntry.originalColumn < 0) {
      throw new Error('Negative absolute value indicate corrupted source map: ' + [
        mappingEntry.generatedColumn < 0 ? 'generatedColumn=' + mappingEntry.generatedColumn : '',
        mappingEntry.originalLine < 0 ? 'originalLine=' + mappingEntry.originalLine : '',
        mappingEntry.originalColumn < 0 ? 'originalColumn=' + mappingEntry.originalColumn : ''
      ].filter(x => x).join(', ') +
        ' at offset ' + start + ' with ' + JSON.stringify(mappings.slice(start, end)));
    }
  }

  function decodeForMapping(mappings: string, start: number, end: number, numBuffer: number[], mappingEntry: Mapping) {
    const bufLen = decodeVLQSpanToArray(mappings, start, end, numBuffer);
    if (bufLen < 4) throw new Error('Incorrect encoded span, ' + bufLen + ' is too short: ' + mappings.slice(start, end) + '.');
    if (bufLen > 5) throw new Error('Incorrect encoded span, ' + bufLen + ' is too long: ' + mappings.slice(start, end) + '.');

    mappingEntry.generatedColumn += numBuffer[0];
    mappingEntry.originalFile += numBuffer[1];
    mappingEntry.originalLine += numBuffer[2];
    mappingEntry.originalColumn += numBuffer[3];

    // Need to carry the last name index even if it's not specified,
    // because it's relative when it is used.
    // For that, it's converted into a negative number (zero becomes -1, 1 becomes -2 etc.) 
    if (bufLen === 5) {
      mappingEntry.originalName =
        (mappingEntry.originalName < 0 ? -mappingEntry.originalName - 1 : mappingEntry.originalName)
        + numBuffer[4];
    }
    else if (mappingEntry.originalName >= 0) {
      mappingEntry.originalName = -mappingEntry.originalName - 1;
    }
  }

  function copyMapping(from: Mapping, to: Mapping) {
    to.generatedLine = from.generatedLine;
    to.generatedColumn = from.generatedColumn;
    to.originalFile = from.originalFile;
    to.originalLine = from.originalLine;
    to.originalName = from.originalName;
  }

  export namespace reader {

    /** note $(?![\r\n])  at the end of regex, which matches the true EOF rather than just EOL */
    const regex_sourcemapUrl = /\/\/[\t ]*#[\t ]*sourceMappingURL[\t ]*=[\t ]*(.+?)(\s*)$(?![\r\n])/m;
    const regex_embeddedDataPrefix = /^data\:application\/json\;base64\,/;

    export declare type SourcemapAndReader = {
      sourcemap: Sourcemap;
      reader: reader;
    };

    export declare namespace findSourcemappingURL {
      export type Result = {
        url: string;
        position: number;

        embeddedSourcemap?: SourcemapAndReader;
      }
    }

    export function findSourcemappingURL(scriptText: string): findSourcemappingURL.Result | undefined {
      const match = scriptText && regex_sourcemapUrl.exec(scriptText);
      if (!match) return void 0;
      const url = match[1];

      const embeddedDataPrefixMatch = regex_embeddedDataPrefix.exec(url);
      if (embeddedDataPrefixMatch) {
        const base64Data = url.slice(embeddedDataPrefixMatch.index + embeddedDataPrefixMatch[0].length);
        try {
          const embeddedSourcemap = fromSourceMap(danfo.parsing.atob(base64Data));
          if (embeddedSourcemap) {
            return {
              url,
              position: scriptText.length - match[2].length - url.length,
              embeddedSourcemap
            };
          }
        }
        catch (error) {
        }
      }

      return {
        url,
        position: scriptText.length - match[2].length - url.length
      };
    }

    export function fromSourceMap(sourcemapContent: string): SourcemapAndReader | undefined {
      if (!sourcemapContent) return void 0;
      let sourcemap: Sourcemap;
      try {
        // TODO do basic sanity check pre-parse to bail out early
        sourcemap = JSON.parse(sourcemapContent);
      }
      catch (error) {
        return void 0;
      }

      if (!sourcemap || typeof sourcemap.mappings !== 'string') return void 0;
      const theReader = reader(sourcemap.mappings);

      return {
        sourcemap,
        reader: theReader
      };
    }
  }
}