namespace danfo.build.code {

  export declare namespace createKeyCache {

    namespace Response {
      type InProgress = { completed: false, initial: boolean, reset(): void };
      type Resolved<TEntry> = { completed: true, success: true, result: TEntry, reset(): void };
      type Failed = { completed: true, success: false, error: any, reset(): void };
    }

    type Cache<TCache> = {
      cachedOrPopulate<TEntry>(options: cachedOrPopulateOptions<TCache, TEntry>): Response<TEntry>;
    }

    type cachedOrPopulateOptions<TCache, TEntry> = {
      cacheField: keyof TCache;
      key?: string;
      populate(): TEntry | Promise<TEntry>;
    };

    type Response<TEntry> = createKeyCache.Response.InProgress | createKeyCache.Response.Resolved<TEntry> | createKeyCache.Response.Failed;
  }

  export function createKeyCache<TCache extends object>(cacheOwner: TCache, promiseWrap: { <T>(work: Promise<T>): Promise<T> } | null): createKeyCache.Cache<TCache> {
    return {
      cachedOrPopulate
    };

    function cachedOrPopulate<TCache, TEntry>({ cacheField, key, populate }: createKeyCache.cachedOrPopulateOptions<TCache, TEntry>): createKeyCache.Response<TEntry> {
      let cache = (cacheOwner as any)[cacheField] as { [key: string]: any };
      if (!cache && typeof key === 'string') {
        cache = {};
        (cacheOwner as any)[cacheField] = cache;
      }

      const entry = (typeof key === 'string' ? cache[key] : cache) as createKeyCache.Response<TEntry> | undefined;
      if (entry) {
        if (!entry.completed && (entry as createKeyCache.Response.InProgress).initial)
          (entry as createKeyCache.Response.InProgress).initial = false;

        return entry;
      }

      const newEntry: createKeyCache.Response.InProgress = {
        completed: false,
        initial: true,
        reset
      };

      if (typeof key === 'string') cache[key] = newEntry;
      else (cacheOwner as any)[cacheField] = newEntry;
      try {
        const populateResult = populate();
        const populateResultPromise = populateResult as unknown as Promise<TEntry>;
        if (populateResult && typeof populateResultPromise.then === 'function') {
          const wrappedPromise = typeof promiseWrap === 'function' ?
            promiseWrap(populateResultPromise) :
            populateResultPromise;

          wrappedPromise.then(
            result => {
              const populatedEntry: createKeyCache.Response.Resolved<TEntry> = {
                completed: true,
                success: true,
                result,
                reset
              };
              if (typeof key === 'string') cache[key] = populatedEntry;
              else (cacheOwner as any)[cacheField] = populatedEntry;
            },
            error => {
              const populatedFail: createKeyCache.Response.Failed = {
                completed: true,
                success: false,
                error,
                reset
              };
              if (typeof key === 'string') cache[key] = populatedFail;
              else (cacheOwner as any)[cacheField] = populatedFail;
            }
          );
          return newEntry;
        }
        else {
          const populatedEntry: createKeyCache.Response.Resolved<TEntry> = {
            completed: true,
            success: true,
            result: populateResult as TEntry,
            reset
          };
          if (typeof key === 'string') cache[key] = populatedEntry;
          else (cacheOwner as any)[cacheField] = populatedEntry;

          return populatedEntry;
        }
      }
      catch (error) {
        const populatedFail: createKeyCache.Response.Failed = {
          completed: true,
          success: false,
          error,
          reset
        };
        if (typeof key === 'string') cache[key] = populatedFail;
        else (cacheOwner as any)[cacheField] = populatedFail;
  
        return populatedFail;
      }

      function reset() {
        delete (cacheOwner as any)[cacheField];
      }
    }
  }
}