namespace danfo.build.code {

  export declare namespace hoistHelpers {
    type Options = {
      ts: typeof import('typescript')
    }
  }

  export function hoistHelpers(options: hoistHelpers.Options) {

    var emitHelpers: { fileName: string, emitHelper: typescript.EmitHelper }[] = [];

    return {
      transform: transform,
      emitHelpers
    };

    function transform(context: typescript.TransformationContext) {

      return transformSourceFile;

      function transformSourceFile(sourceFileNode: typescript.SourceFile): typescript.SourceFile {

        let fileNodeEmitHelpers = options.ts.getEmitHelpers(sourceFileNode);
        if (fileNodeEmitHelpers) {
          // removing will affect this same array instance,
          // so iterate through a copy instead
          fileNodeEmitHelpers = fileNodeEmitHelpers.slice();

          for (const h of fileNodeEmitHelpers) {
            emitHelpers.push({ fileName: sourceFileNode.fileName, emitHelper: h });
            options.ts.removeEmitHelper(sourceFileNode, h);
          }
        }

        return sourceFileNode;
      }
    }
  }
}