namespace danfo.node {

  export type FileSystemWithBinary = danfo.build.FileSystem & {
    readFile: {
      (file: string): string | undefined;
      (file: string, encoding: 'binary'): Buffer | readonly number[] | undefined;
    }
  }

  export type FileSystemTrackingChanges = {
    fs: FileSystemWithBinary;
    onChanges?: (allChanges: string[]) => void;
    next(): void;
    stopWatching(): void;
  }

  export declare namespace createFS {
    type DebugOptions = {
      fs: typeof import('fs');
      path: typeof import('path');
    }
  }

  function doNothing() { }

  export function createFS(rootDir: string, options?: Partial<createFS.DebugOptions>) {

    const changesTimeoutMS = 50;
    const recurringMissingCheckTimeoutMS = 10000;

    const fs = options && options.fs || require('fs') as typeof import('fs');
    if (options && !options.fs) options.fs = fs;
    const path = options && options.path || require('path') as typeof import('path');
    if (options && !options.path) options.path = path;
    let needsWatching = true;

    type WatchingEntry = {
      close(): void;
      latestStat?: danfo.build.FileSystem.Stat;
      changed: boolean;
      missing: boolean;
    };

    let session = createSession();

    let fsWithChanges: FileSystemTrackingChanges = {
      fs: {
        basePath: path.resolve(rootDir),
        readDirectory,
        readFile,
        getRelativePath,
        resolvePath,
        stat,
        writeFile
      },
      next,
      stopWatching
    };


    return fsWithChanges;

    function stopWatching() {
      if (!needsWatching) return;
      needsWatching = false;
      if (session.watchingMap) {
        for (const k in session.watchingMap) {
          const entry = session.watchingMap[k];
          if (entry && typeof entry.close === 'function')
            entry.close();
        }
        session.watchingMap = {};
      }
      if (session.recurringMissingCheckTimeout) {
        clearTimeout(session.recurringMissingCheckTimeout);
        session.recurringMissingCheckTimeout = void 0;
      }
    }

    function createSession() {
      return {
        watchingMap: {} as { [resolvedPath: string]: WatchingEntry },
        allChangesList: [] as string[],
        onChangesTimeout: null as any,
        recurringMissingCheckTimeout: null as any
      };
    }

    function readDirectory(dirPath: string): string[] | undefined {
      const resolvedFullDirPath = resolvePath(dirPath);
      watchPath(resolvedFullDirPath);
      try {
        const dirFiles = fs.readdirSync(resolvedFullDirPath);
        const resolvedResults = [];
        for (const f of dirFiles) {
          const res = path.normalize(path.resolve(resolvedFullDirPath, f));
          resolvedResults.push(res);
        }
        return resolvedResults;
      }
      catch (noAccessOrDoesNotExist) {
      }
    }

    function readFile(file: string): string | undefined;
    function readFile(file: string, encoding: 'binary'): Buffer | readonly number[] | undefined;
    function readFile(filePath: string, encoding?: 'binary'): string | Buffer | readonly number[] | undefined {
      const resolvedFullPath = resolvePath(filePath);
      watchPath(resolvedFullPath);
      try {
        if (encoding === 'binary')
          return fs.readFileSync(resolvedFullPath);

        const buf = fs.readFileSync(resolvedFullPath, { encoding });
        return buf.toString(encoding || 'utf8');
      }
      catch (noAccessOrDoesNotExist) { }
    }

    function writeFile(filePath: string, data: string | Buffer, encoding?: 'binary'): void {
      const resolvedFullPath = resolvePath(filePath);
      fs.writeFileSync(resolvedFullPath, data, encoding);
      unwatchPathOnWrite(filePath, data.length);
    }

    function getRelativePath(resolvedBaseDir: string, resolvedFileName: string): string {
      return path.relative(resolvedBaseDir, resolvedFileName);
    }

    function resolvePath(path1: string, path2?: string) {
      // note a forced normalize is required (is it only on some versions?)
      const resolvedPath = path.normalize(
        path2 ? path.resolve(fsWithChanges.fs.basePath, path1, path2) :
          path.resolve(fsWithChanges.fs.basePath, path1)
      );

      return resolvedPath;
    }

    function stat(filePath: string): danfo.build.FileSystem.Stat | undefined {
      const resolvedFullPath = resolvePath(filePath);
      const stat = watchPath(resolvedFullPath);
      // console.log('stat(', filePath, ') ', stat);
      return stat;
    }

    function next() {
      if (!needsWatching)
        return;
        
      if (session.onChangesTimeout) {
        clearTimeout(session.onChangesTimeout);
        session.onChangesTimeout = null;
      }
        
      for (const resolveFullPath in session.watchingMap) {
        const entry = session.watchingMap[resolveFullPath];
        if (entry && typeof entry.close === 'function') {
          try {
            entry.close();
          }
          catch { }
        }
      }

      session = createSession();
    }

    function deriveTimestamp(fsStat: import('fs').Stats): number | undefined {
      const timestampM = statTimestamp(fsStat.mtimeMs, fsStat.mtime);
      const timestampC = statTimestamp(fsStat.ctimeMs, fsStat.ctime);
      const timestampBirth = statTimestamp(fsStat.birthtimeMs, fsStat.birthtime);
      return Math.max(timestampM!, timestampC!, timestampBirth!);
    }

    function statTimestamp(xtimeMs: number | undefined, xtime: Date | undefined): number | null {
      if (typeof xtimeMs === 'number')
        return xtimeMs;
      else if (xtime && typeof xtime.getTime === 'function')
        return xtime.getTime();
      return null;
    }

    function watchPath(resolvedFullPath: string, refreshStat?: boolean): danfo.build.FileSystem.Stat | undefined {
      if (!needsWatching) {
        try { return statDirect(resolvedFullPath); }
        catch { return; }
      }
        
      const existingWatchingEntry = session.watchingMap[resolvedFullPath];
      if (existingWatchingEntry) {
        if (!refreshStat && existingWatchingEntry.latestStat && !existingWatchingEntry.changed)
          return existingWatchingEntry.latestStat;

        try {
          const currentStat = statDirect(resolvedFullPath);
          if (currentStat && !existingWatchingEntry.latestStat)
            existingWatchingEntry.latestStat = currentStat;
            
          return currentStat;
        }
        catch {
          watchNoteMissing(resolvedFullPath);
          return;
        }
      }

      let currentStat: danfo.build.FileSystem.Stat;
      let fsWatcher: import('fs').FSWatcher | undefined;
      let entry: WatchingEntry | undefined;
      try {
        currentStat = statDirect(resolvedFullPath);
        fsWatcher = fs.watch(resolvedFullPath, { recursive: false }, (event, filename) => {
          if (entry) {
            if (!entry.changed) {
              entry.changed = true;
              session.allChangesList.push(resolvedFullPath);
            }

            queueOnChanges();
          }
        });
      }
      catch (statError) {
        if (fsWatcher)
          fsWatcher.close();

        watchNoteMissing(resolvedFullPath);
        return;
      }
        
      entry = {
        close: closeWatcher,
        changed: false,
        missing: false,
        latestStat: currentStat
      };

      session.watchingMap[resolvedFullPath] = entry;

      return currentStat;

      function closeWatcher() {
        if (fsWatcher)
          fsWatcher.close();
        entry!.close = doNothing;
      }
    }

    function unwatchPathOnWrite(resolvedFullPath: string, length: number) {
      if (!needsWatching) return;
      const latestStat = {
        isDirectory: false,
        length,
        timestamp: Date.now()
      };

      const existingWatchingEntry = session.watchingMap[resolvedFullPath];
      if (existingWatchingEntry) {
        existingWatchingEntry.changed = false;
        existingWatchingEntry.latestStat = latestStat;
        existingWatchingEntry.close();
      }
      else {
        const entry: WatchingEntry = {
          close: doNothing,
          changed: false,
          missing: false,
          latestStat
        };
        session.watchingMap[resolvedFullPath] = entry;
      }
    }

    function queueOnChanges() {
      if (session.onChangesTimeout)
        clearTimeout(session.onChangesTimeout);
        
      session.onChangesTimeout = setTimeout(invokeOnChanges, changesTimeoutMS);
    }

    function invokeOnChanges() {
      session.onChangesTimeout = null;
      const arg = session.allChangesList;
      if (typeof fsWithChanges.onChanges === 'function') {
        // reset changes for the next watch result
        session.allChangesList = [];
        for (const key in session.watchingMap) {
          const entry = session.watchingMap[key];
          if (entry && entry.changed) {
            entry.changed = false;
            entry.latestStat = void 0;
          }
        }

        fsWithChanges.onChanges(arg);        
      }
    }

    function watchNoteMissing(resolvedFullPath: string): void {
      let entry = session.watchingMap[resolvedFullPath];
      if (entry && entry.missing)
        return;

      {
        // walk up the directories until not missing anymore
        // (to avoid watching many missing paths within the same missing directory)
        while (true) {
          const parentPath = path.dirname(resolvedFullPath);
          if (!parentPath || parentPath === resolvedFullPath || parentPath === '/') {
            break;
          }
          const parentEntry = session.watchingMap[parentPath];
          if (parentEntry) {
            if (parentEntry.missing) return;
            resolvedFullPath = resolvedFullPath;
            break;
          }
          else {
            if (fs.existsSync(parentPath))
              break;
          }

          resolvedFullPath = parentPath;
        }
      }
      

      if (entry && typeof entry.close === 'function') {
        try {
          entry.close();
        }
        catch { }
      }

      if (entry) {
        entry.missing = true;
      }
      else {
        entry = {
          changed: false,
          missing: true,
          close: doNothing
        };
        session.watchingMap[resolvedFullPath] = entry;
      }

      entry.missing = true;
      if (session.recurringMissingCheckTimeout)
        clearTimeout(session.recurringMissingCheckTimeout);
        
      session.recurringMissingCheckTimeout = setTimeout(
        retryMissing,
        recurringMissingCheckTimeoutMS
      );
    }

    function retryMissing() {
      session.recurringMissingCheckTimeout = null;

      for (const resolvedFullPath in session.watchingMap) {
        const entry = session.watchingMap[resolvedFullPath];
        if (entry && entry.missing) {
          try {
            const renewedStat = statDirect(resolvedFullPath);
            entry.missing = false;
            if (!entry.changed) {
              session.allChangesList.push(resolvedFullPath);
              // TODO: maybe even restart watching?
              queueOnChanges();
            }
          }
          catch (missingAgain) {
            // OK, do nothing...
          }
        }
      }

      clearTimeout(session.recurringMissingCheckTimeout);
      session.recurringMissingCheckTimeout = setTimeout(
        retryMissing,
        recurringMissingCheckTimeoutMS
      );
    }

    function statDirect(resolvedFullPath: string) {
      const fsStat = fs.statSync(resolvedFullPath);
      return {
        isDirectory: fsStat.isDirectory(),
        length: fsStat.size,
        timestamp: deriveTimestamp(fsStat) || 0
      };
    }
  }

}