namespace danfo.build {


  export declare namespace projectBuilder {
    type Options = {
      projRoot?: string;
      trackingFileSystem: danfo.node.FileSystemTrackingChanges;
      outputPath?: string;
      watch?: boolean;
      sources: string[];

      onBuildStart?: (args: OnBuildStartArgs) => void;
      onBuildFinish?: (args: OnBuildFinishArgs) => void;

    } & danfo.build.code.codeBuilder.CacheOptions;

    type OnBuildStartArgs = populateSourceStructure.Result & {
      outputDirPath: string;
      changes?: string[];
      html: boolean;
    };

    type OnBuildFinishArgs = populateSourceStructure.Result & {
      outputDirPath: string;
      success: boolean;
      buildTime: number;
      html: boolean;
      emits: BuildEmit[];
      unresolvedImports?: danfo.build.html.UnresolvedImports[];
      errors: build.code.BuildError[];
    }; 

    type BuildEmit = {
      localPath: string;
      fullPath: string;
      content: string | { [byteIndex: number]: number, length: number };
      changedFrom?: {
        timestamp?: number;
        content?: string | { [byteIndex: number]: number, length: number };
      }
    }

    type Builder = {
      stop(): void;
    }
  }


  export function projectBuilder(options: projectBuilder.Options) {

    let htmlBuilderInstance: danfo.build.html.Builder | undefined;
    let bundlerInstance: danfo.build.bundle.Bundler | undefined;

    let firstBuild = true;

    if (options.watch !== false) options.trackingFileSystem.onChanges = handleChanges;

    let resolvedOutputDirPath = getOutputDirPath(options.outputPath);
    let preciseEmitPaths: string[] | undefined;
    
    const excludeOutputCheck = (dirPath: string) => {
      return (
        preciseEmitPaths ?
          preciseEmitPaths.indexOf(dirPath) >= 0 :
          dirPath.indexOf(resolvedOutputDirPath) === 0
      );
    };


    buildOrRebuild();
    firstBuild = false;

    function getOutputDirPath(outputPath?: string) {
      let probePath = options.trackingFileSystem.fs.resolvePath(outputPath || 'dist');
      const stat = options.trackingFileSystem.fs.stat(probePath);
      if (!stat || stat.isDirectory) return probePath;
      else return options.trackingFileSystem.fs.resolvePath(probePath, '..');
    }

    function handleChanges(allChanges: string[]) {
      if (allChanges) {
        let anyRealChanges: string[] | undefined;
        for (const ch of allChanges) {
          if (!excludeOutputCheck(ch)) {
            if (!anyRealChanges) anyRealChanges = [ch];
            else anyRealChanges.push(ch);
          }
        }
        if (!anyRealChanges) return;
        else allChanges = anyRealChanges
      }

      buildOrRebuild({ allChanges });
    }

    function buildOrRebuild(reason?: { allChanges: string[] }) {
      const sourceStructure = populateSourceStructure({
        fileSystem: options.trackingFileSystem.fs,
        projectPath: options.projRoot,
        sources: options.sources,
        exclude: excludeOutputCheck
      });

      if (!options.outputPath)
        resolvedOutputDirPath = getOutputDirPath(
          sourceStructure.projectDir ? options.trackingFileSystem.fs.resolvePath(sourceStructure.projectDir, 'dist') : void 0);

      let html: SourceFileOrSpan | undefined;
      for (const srcEntry of sourceStructure.sources) {
        if (srcEntry.type === 'html') {
          html = srcEntry;
          break;
        }
      }

      if (typeof options.onBuildStart === 'function')
        options.onBuildStart({
          ...sourceStructure,
          outputDirPath: resolvedOutputDirPath,
          changes: reason && reason.allChanges,
          html: !!html
        });
      
      if (html) {
        buildForHtml(sourceStructure, html);
      }
      else {
        if (firstBuild && typeof options.watch !== 'boolean') {
          // no need for watching, if neither asked nor can infer from the presence of HTML
          options.trackingFileSystem.stopWatching();
        }

        buildForJS(sourceStructure);
      }
    }

    function buildForJS(sourceStructure: populateSourceStructure.Result) {
      if (!bundlerInstance) bundlerInstance = danfo.build.bundle.codeBundler({
        fileSystem: options.trackingFileSystem.fs
      });

      const buildStart = Date.now();

      const bundleBuildResult = bundlerInstance.build({
        outputDir: resolvedOutputDirPath,
        resolvedProjectDir: sourceStructure.projectDir || options.projRoot!,
        sources: sourceStructure.sources as SourceFileOrSpan[]
      });
      if (bundleBuildResult.outputFile)
        resolvedOutputDirPath = options.trackingFileSystem.fs.resolvePath(resolvedOutputDirPath, bundleBuildResult.outputFile + '/..');

      const emits: projectBuilder.BuildEmit[] = [];
      if (bundleBuildResult.outputFile) {
        if (bundleBuildResult.output)
          emits.push(createEmit(bundleBuildResult.outputFile, bundleBuildResult.output));
        if (bundleBuildResult.sourcemap)
          emits.push(createEmit(bundleBuildResult.sourcemap.fileName, bundleBuildResult.sourcemap.content));
        if (bundleBuildResult.typings)
          emits.push(createEmit(bundleBuildResult.typings.fileName, bundleBuildResult.typings.content));
      }

      const errors = concatWithMissingFileErrors(bundleBuildResult.errors, sourceStructure);

      const buildTime = Date.now() - buildStart;

      const buildResult: projectBuilder.OnBuildFinishArgs = {
        ...sourceStructure,
        success: !!bundleBuildResult.outputFile,
        outputDirPath: resolvedOutputDirPath,
        buildTime,
        html: false,
        emits,
        unresolvedImports: bundleBuildResult.imports,
        errors
      };

      preciseEmitPaths = emits.map(em => em.fullPath);

      if (typeof options.onBuildFinish === 'function')
        options.onBuildFinish(buildResult);
    }

    function buildForHtml(sourceStructure: populateSourceStructure.Result, htmlEntry: SourceFileOrSpan) {
      if (!htmlBuilderInstance)
        htmlBuilderInstance = danfo.build.html.htmlBuilder({
          fileSystem: options.trackingFileSystem.fs,
          precachedFixedSource: options.precachedFixedSource
        });

      const buildStart = Date.now();

      const htmlBuildResults = htmlBuilderInstance.build({
        resolvedProjectDir: sourceStructure.projectDir!,
        htmlFile: htmlEntry,
        otherFiles: sourceStructure.sources,
        outputDirPath: resolvedOutputDirPath
      });

      let outputHtmlPath: string;
      if (options.outputPath) {
        const probePath = options.trackingFileSystem.fs.resolvePath(options.outputPath);
        const stat = options.trackingFileSystem.fs.stat(probePath);
        if (!stat || stat.isDirectory) {
          outputHtmlPath = options.trackingFileSystem.fs.resolvePath(options.outputPath, getBaseName(htmlEntry.resolvedPath));
        }
        else {
          outputHtmlPath = probePath;
        }
      }
      else {
        outputHtmlPath = options.trackingFileSystem.fs.resolvePath(resolvedOutputDirPath, getBaseName(htmlEntry.resolvedPath));
      }

      const emits: projectBuilder.BuildEmit[] = [];
      if (htmlBuildResults.htmlEmit)
        emits.push(createEmit(outputHtmlPath, htmlBuildResults.htmlEmit));

      if (htmlBuildResults.resources) {
        for (const res of htmlBuildResults.resources) {
          const resPath = options.trackingFileSystem.fs.resolvePath(resolvedOutputDirPath, res.name);
          emits.push(createEmit(resPath, res.content));
        }
      }

      const errors = concatWithMissingFileErrors(htmlBuildResults.errors, sourceStructure);

      const buildTime = Date.now() - buildStart;

      const buildResult: projectBuilder.OnBuildFinishArgs = {
        ...sourceStructure,
        success: typeof htmlBuildResults.htmlEmit === 'string',
        outputDirPath: resolvedOutputDirPath,
        buildTime,
        html: true,
        emits,
        unresolvedImports: htmlBuildResults.unresolvedImports,
        errors
      };

      preciseEmitPaths = emits.map(em => em.fullPath);

      if (typeof options.onBuildFinish === 'function')
        options.onBuildFinish(buildResult);
    }

    function createEmit(fullPath: string, content: string) {
      let localPath = options.trackingFileSystem.fs.getRelativePath(resolvedOutputDirPath, fullPath).replace(/\\/g, '/');
      if (/^\//.test(localPath)) localPath = localPath.slice(1);
      const currentStat = options.trackingFileSystem.fs.stat(fullPath);

      if (!currentStat || currentStat.isDirectory) {
        return {
          localPath,
          fullPath,
          content,
          changedFrom: {}
        };
      }

      const currentContent = options.trackingFileSystem.fs.readFile(fullPath);

      return content === currentContent ?
        {
          localPath,
          fullPath,
          content
        }
        : {
          localPath,
          fullPath,
          content,
          changedFrom: {
            timestamp: currentStat.timestamp,
            content: currentContent
          }
        };
    }
  }


  function concatWithMissingFileErrors(errors: code.BuildError[], sourceStructure: populateSourceStructure.Result) {
    if (sourceStructure.missing?.length) {
      errors = [...errors];
      for (const miss of sourceStructure.missing) {
        errors.push({
          code: 'MISS',
          message: 'Cannot find file ' + (miss.originalSourcePath || miss.resolvedPath),
          resolvedSourcePath: miss.originalSourcePath || miss.resolvedPath
        });
      }
    }

    return errors;
  }
}