namespace danfo.parsing.cmdline {
  export function derivePort(projRoot: string): number {
    return 4500 + Math.abs(derivePort.strHashCode(projRoot)) % 5000;
  }

  export namespace derivePort {

    export function strHashCode(str: string) {
      let sum = 127653294848;
      for (let i = 0; i < str.length; i++) {
        sum = ((sum << 5) - sum) + str.charCodeAt(i);
        sum = sum & sum;
      }
      return sum;
    }
  }

}