namespace danfo.build.html {

  type ParsedEntry = {
    parsed: danfo.parsing.parseHTML.ReferenceOrContent;
    isDirectory?: boolean;
    isTsConfig?: boolean;
    syntheticResolvedFileName?: string;
    emitted?: {
      output?: string;
      sourcemap?: { fileName: string, content: string };
    };
  };

  const regex_LF_withPossibleTrailingWhitespace_thenEOF = /\n\s*$/;
  const regex_leadingLineHasNonWhitespace = /^[^\S\r\n]*\S/;
  const regex_trailingLineHasNonWhitespace = /\S[^\S\r\n]*$/;

  export function htmlBuilder({ fileSystem, precachedFixedSource: precachedFixedSnapshots }: htmlBuilder.Options): Builder {

    let htmlFile!: danfo.build.SourceFileOrSpan;
    let resolvedHtmlFilePath!: string;
    let resolvedProjectDir!: string;

    let onDemandCodeBuilder: danfo.build.code.Builder | undefined;
    let processEntries: ParsedEntry[] = [];

    const builder: Builder = {
      build: buildHtml
    };

    return builder;

    function buildHtml(inputs: BuilderInputs): BuildResult {
      htmlFile = inputs.htmlFile;
      resolvedProjectDir = inputs.resolvedProjectDir;
      resolvedHtmlFilePath = htmlFile.resolvedPath;

      const htmlContent = fileSystem.readFile(resolvedHtmlFilePath)!;
      if (typeof htmlContent !== 'string')
        return {
          htmlEmit: void 0,
          errors: [{
            code: 'No file',
            message: 'File cannot be read from ' + resolvedHtmlFilePath,
            resolvedSourcePath: resolvedHtmlFilePath
          }]
        }

      const htmlParsed = danfo.parsing.parseHTML(htmlContent);
      const errors: danfo.build.code.BuildError[] = [];

      processEntries = htmlParsed.map(parsed => {
        const entry: ParsedEntry = {
          parsed
        };
        return entry
      });

      extractAndProcessCssEntries();

      const codeSources: SourceFileOrSpan[] = [];
      const codeEntriesByResolvedPath: { [resolvedFileName: string]: ParsedEntry } = {};
      let htmlContentLineMap!: parsing.lineMap.Output;
      for (const entry of processEntries) {
        if (!entry.parsed || !htmlBuilder.isCompileScriptType(entry.parsed.type))
          continue;

        let content = entry.parsed.content;
        let resolvedPath: string | undefined;
        if (typeof content !== 'string' && typeof entry.parsed.reference === 'string') {
          // reference is relative to the HTML file directory
          resolvedPath = fileSystem.resolvePath(htmlFile.resolvedPath + '/../' + entry.parsed.reference);

          const entryStat = fileSystem.stat(resolvedPath);
          if (!entryStat) {
            errors.push({
              code: 'File not found',
              message: 'Cannot read referenced file ' + resolvedPath + '.'
            });
          }
          else if (entryStat.isDirectory) {
            entry.isDirectory = true;
            const dirContent = danfo.build.populateSourceStructure({
              projectPath: inputs.resolvedProjectDir,
              fileSystem,
              sources: [resolvedPath]
            });
            for (const dirEntry of dirContent.sources) {
              codeSources.push(dirEntry);
            }
          }
          else {
            content = fileSystem.readFile(resolvedPath);
          }
        }

        if (typeof content === 'string') {
          if (typeof resolvedPath !== 'string') {
            resolvedPath = generateSyntheticFilePath(entry.parsed);
            entry.syntheticResolvedFileName = resolvedPath;
          }

          codeEntriesByResolvedPath[resolvedPath] = entry;
          if (entry.parsed.type === 'tsconfig')
            entry.isTsConfig = true;

          codeSources.push({
            resolvedPath,
            type: entry.parsed.type,
            stat: inputs.htmlFile.stat,
            span: {
              content,
              start: entry.parsed.start + entry.parsed.originalLeadLength,
              length: entry.parsed.length - entry.parsed.originalLeadLength - entry.parsed.originalTrailLength
            }
          });
        }
      }

      let unresolvedImports: UnresolvedImports[] | undefined;
      let externalEmits: code.Emit[] | undefined;

      for (const src of inputs.otherFiles) {
        codeSources.push(src as SourceFileOrSpan);
      }

      if (codeSources.length) {
        const builtCode = runCodeBuilder({
          resolvedProjectDir: inputs.resolvedProjectDir,
          sources: codeSources,
          handleSettings: settings => {
            settings.outDir = inputs.outputDirPath;
            return settings;
          }
        });

        if (builtCode.imports) {
          // TODO: handle imports?
          unresolvedImports = builtCode.imports;
        }

        if (builtCode.errors) {
          for (const err of builtCode.errors) {
            errors.push(err);
          }
        }

        if (builtCode.emits) {
          for (const emit of builtCode.emits) {
            let entry = codeEntriesByResolvedPath[emit.resolvedSourcePath];
            if (!entry) {
              if (!externalEmits) externalEmits = [];
              externalEmits.push(emit);
            }
            else {
              entry.emitted = {
                output: emit.output,
                sourcemap: emit.sourcemap
              };
            }
          }
        }
      }

      const { htmlEmit, resources } = combineEmits(
        htmlContent,
        processEntries,
        externalEmits);

      return {
        htmlEmit,
        resources,
        errors,
        unresolvedImports: unresolvedImports?.length ? unresolvedImports : void 0
      };

      function extractAndProcessCssEntries() {
        for (const entry of processEntries) {
          if (!entry.parsed || entry.parsed.type !== 'css')
            continue;

          let content = entry.parsed.content;
          if (typeof content !== 'string' && typeof entry.parsed.reference === 'string') {
            // local to the directory containing HTML file
            const resolvedFileName = fileSystem.resolvePath(htmlFile.resolvedPath + '/../' + entry.parsed.reference);
            content = fileSystem.readFile(resolvedFileName);
            if (typeof content !== 'string') {
              errors.push({
                code: 'File not found',
                message: 'Cannot read referenced file ' + resolvedFileName + '.'
              });
            }
          }

          if (typeof content === 'string') {
            const processedCSS = processCSS(content);
            entry.emitted = {
              output: processedCSS
            };
          }
        }
      }

      function generateSyntheticFilePath(parsed: danfo.parsing.parseHTML.ReferenceOrContent): string {
        if (!htmlContentLineMap)
          htmlContentLineMap = parsing.lineMap(htmlContent);
        
        let contentStart = parsed.start + parsed.originalLeadLength;
        const firstContentChar = htmlContent.charCodeAt(contentStart);
        if (firstContentChar === 10) {
          contentStart++;
        }
        else if (firstContentChar === 13) {
          const secondContentChar = htmlContent.charCodeAt(contentStart + 1);
          if (secondContentChar === 10)
            contentStart += 2;
          else
            contentStart++;
        }

        const lnch = htmlContentLineMap.getLineAndCharacter(contentStart)!;
        const reference =
          htmlFile.resolvedPath + '.L' + lnch.ln +
          (lnch.ch > 1 ? '-' + lnch.ch : '') +
          (parsed.type === 'typescript' ? '.tsx' : '.js');
        
        return reference;
      }

      function processCSS(rawCSS: string): string {
        // TODO: build css
        return rawCSS;
      }

    }

    function runCodeBuilder(inputs: code.BuilderInputs): code.BuildResult {
      if (!onDemandCodeBuilder)
        onDemandCodeBuilder = code.codeBuilder({
          fileSystem: {
            ...fileSystem,
            readFile: overrideReadFile,
            stat: overrideStat
          },
          precachedFixedSource: precachedFixedSnapshots
        });

      return onDemandCodeBuilder.build(inputs);

      function getSyntheticFile(resolvedFilePath: string) {
        for (const entry of processEntries) {
          if (entry.syntheticResolvedFileName === resolvedFilePath && typeof entry.parsed.content === 'string')
            return entry;
        }
      }

      function overrideReadFile(filePath: string) {
        const resolvedFilePath = fileSystem.resolvePath(resolvedProjectDir, filePath)
        const entry = getSyntheticFile(resolvedFilePath);
        if (entry)
          return entry.parsed.content!;

        return fileSystem.readFile(filePath);
      }

      function overrideStat(filePath: string) {
        const resolvedFilePath = fileSystem.resolvePath(resolvedProjectDir, filePath);
        const entry = getSyntheticFile(resolvedFilePath);
        if (entry) {
          return {
            isDirectory: false,
            timestamp: htmlFile.stat?.timestamp || 0,
            length: entry.parsed.content!.length
          };
        }

        return fileSystem.stat(filePath);
      }
    }

    function combineEmits(htmlContent: string, processEntries: ParsedEntry[], allExternalEmits?: code.Emit[]): { htmlEmit: string, resources?: { name: string, content: string }[] } {
      let htmlEmit = '';
      let resources: { name: string, content: string }[] | undefined;

      let lastChunkEnd = 0;
      let injectAfterList: code.Emit[] | undefined;
      let injectExternalEmitsIntoNextScript: code.Emit[] | undefined;
      if (allExternalEmits) {
        for (const em of allExternalEmits) {
          if (em.injectAfter) {
            if (!injectAfterList) injectAfterList = [];
            injectAfterList.push(em);
          }
          else {
            if (!injectExternalEmitsIntoNextScript) injectExternalEmitsIntoNextScript = [];
            injectExternalEmitsIntoNextScript.push(em);
          }
        }
      }

      for (let i = 0; i < processEntries.length; i++) {
        const entry = processEntries[i];
        addEntry(entry, i === processEntries.length - 1);
      }

      if (lastChunkEnd < htmlContent.length) {
        htmlEmit += htmlContent.slice(lastChunkEnd);
      }

      return { htmlEmit, resources };

      function needsNewLineInjection(entry: ParsedEntry) {
        const leadText = htmlContent.slice(Math.max(0, entry.parsed.start - 15), entry.parsed.start);
        return !leadText || regex_LF_withPossibleTrailingWhitespace_thenEOF.test(leadText);
      }

      function addEntry(entry: ParsedEntry, last: boolean) {

        let emitResult: { output: string, sourcemap?: { fileName: string, content: string } } | undefined;
        if (typeof entry.emitted?.output === 'string')
          emitResult = getEmittedEntryContent(entry, last);
        else if (entry.isTsConfig)
          emitResult = getTsConfigEntryContent(entry);
        else if (entry.isDirectory)
          emitResult  = getDirectoryEntryContent(entry);
        
        if (emitResult) {
          htmlEmit +=
            htmlContent.slice(lastChunkEnd, entry.parsed.start) +
            (entry.parsed.lead || '') +
            emitResult.output +
            (entry.parsed.trail || '');

          lastChunkEnd = entry.parsed.start + entry.parsed.length;

          addEntrySourcemap(entry, emitResult.sourcemap);
        }
      }

      function getEmittedEntryContent(entry: ParsedEntry, last: boolean): { output: string, sourcemap?: { fileName: string, content: string } } | undefined {
        if (typeof entry.emitted?.output !== 'string') return; // this should be redundant

        const injectGraciousNewLines = needsNewLineInjection(entry);
        const shouldInjectExternalEmits =
          (injectExternalEmitsIntoNextScript || (last && injectAfterList)) &&
          (!entry.parsed.type || htmlBuilder.isCompileScriptType(entry.parsed.type));

        if (shouldInjectExternalEmits) {
          let emits =
            [
              ...(!injectExternalEmitsIntoNextScript ? [] :
                [
                  injectGraciousNewLines ? '\n' : '',
              ...injectExternalEmitsIntoNextScript!
                ]),
              entry.emitted,
              ...(!(last && injectAfterList) ? [] :
                injectAfterList
              )
            ];

          injectExternalEmitsIntoNextScript = void 0;
          if (last) injectAfterList = void 0;

          return combineExternalEmits(
            entry,
            emits);
        }

        if (entry.emitted.sourcemap && injectGraciousNewLines)
          return surroundNewLinesAdjustSourcemap(entry.emitted.output, entry.emitted.sourcemap, entry);
        else if (injectGraciousNewLines)
          return { output: wrapNewLines(entry.emitted.output) };
        else
          return { output: entry.emitted.output, sourcemap: entry.emitted.sourcemap };
      }

      function surroundNewLinesAdjustSourcemap(output: string, sourcemap: { fileName: string, content: string }, entry: ParsedEntry) {
        const needsLead = regex_leadingLineHasNonWhitespace.test(output);
        const needsTrail = regex_trailingLineHasNonWhitespace.test(output);

        if (!needsLead && !needsTrail) {
          return { output, sourcemap };
        }

        if (needsLead) {
          let sourcemapJson: danfo.parsing.sourcemap.Sourcemap;
          try {
            sourcemapJson = JSON.parse(sourcemap.content);
          }
          catch (parseSourcemapError) {
            return { output, sourcemap };
          }

          sourcemapJson.mappings = danfo.parsing.sourcemap.combineMappings([{
            generatedLine: 1,
            generatedColumn: 0,
            fileCount: 0,
            nameCount: 0,
            mappings: sourcemapJson.mappings
          }]);

          sourcemap = {
            fileName: sourcemap.fileName,
            content: JSON.stringify(sourcemapJson)
          };
        }

        return {
          output: (needsLead ? '\n' : '') + output + (needsTrail ? '\n' : ''),
          sourcemap
        };
      }

      function wrapNewLines(content: string) {
        const needsLead = regex_leadingLineHasNonWhitespace.test(content);
        const needsTrail = regex_trailingLineHasNonWhitespace.test(content);

        if (!needsLead && needsTrail)
          return content;
        else
          return (needsLead ? '\n' : '') + content + (needsTrail ? '\n' : '');
      }

      function getTsConfigEntryContent(entry: ParsedEntry): { output: string, sourcemap?: { fileName: string, content: string } } {

        const injectGraciousNewLines = needsNewLineInjection(entry);
        let output =
          (injectGraciousNewLines ? '\n' : '') +
          '/* ' + entry.parsed.reference + ' */';
        if (typeof entry.parsed.content === 'string') {
          output =
            '/* ' + (entry.parsed.reference || 'embedded tsconfig.json') +
            '\n' + entry.parsed.content.replace(/\*\//g, '*\\/') + '*/';
        }
        else {
          if (typeof entry.parsed.reference === 'string') {
            const originalContent = fileSystem.readFile(fileSystem.resolvePath(resolvedProjectDir, entry.parsed.reference));
            if (typeof originalContent === 'string')
              output =
                '/* ' + entry.parsed.reference +
                '\n' + originalContent.replace(/\*\//g, '*\\/') + '*/';
          }
        }

        if (injectExternalEmitsIntoNextScript) {
          if (injectExternalEmitsIntoNextScript) {
            const emits = [output, ...injectExternalEmitsIntoNextScript];
            injectExternalEmitsIntoNextScript = void 0;
            return combineExternalEmits(entry, emits);
          }
        }

        return { output };
      }

      function getDirectoryEntryContent(entry: ParsedEntry): { output: string, sourcemap?: { fileName: string, content: string } } {
        const injectGraciousNewLines = needsNewLineInjection(entry);
        let header =
          (injectGraciousNewLines ? '\n' : '') +
          '/* ' + (entry.parsed.reference || 'directory') + ' */';
        if (injectExternalEmitsIntoNextScript) {
          const emits = [header, ...injectExternalEmitsIntoNextScript];
          injectExternalEmitsIntoNextScript = void 0;

          return combineExternalEmits(entry, emits);
        }

        return { output: header + (injectGraciousNewLines ? '\n' : '') };
      }

      function addEntrySourcemap(entry: ParsedEntry, sourcemap = entry.emitted?.sourcemap) {
        if (sourcemap) {
          (resources || (resources = [])).push({
            name: sourcemap.fileName,
            content: sourcemap.content
          });
        }
      }

      function combineExternalEmits(
        entry: ParsedEntry,
        externalEmits: (string | { output?: string, sourcemap?: { fileName: string, content: string } } | undefined)[]) {

        const result = danfo.parsing.sourcemap.concatenate(
          { fileName: getBaseName(entry.syntheticResolvedFileName || entry.parsed.reference!) },
          externalEmits
        );

        return typeof result === 'string' ? { output: result } : result;
      }
    }

  }

  export namespace htmlBuilder {

    const regex_compileScriptType = /javascript|typescript|json|tsconfig|directory/i;

    export function isCompileScriptType(type: string): type is 'javascript' | 'typescript' | 'json' | 'tsconfig' {
      return regex_compileScriptType.test(type);
    }

  }

}