namespace danfo.parsing.sourcemap {

  export declare namespace combineMappings {

    type MappingEntry = {
      generatedLine: number;
      generatedColumn: number;
      mappings: string;
      fileCount: number;
      nameCount: number;
    }

  }

  export function combineMappings(mappingsList: combineMappings.MappingEntry[]) {
    const output = writer();

    let veryFirstLine = true;
    let prevCombinedFileCount = 0;
    let prevCombinedNameCount = 0;
    for (const entry of mappingsList) {
      for (const input = reader(entry.mappings); input.read();) {
        input.generatedLine += entry.generatedLine;
        if (veryFirstLine) {
          // for concatenated scripts, only the very first line of the first script is bumped
          input.generatedColumn += entry.generatedColumn;
          veryFirstLine = false;
        }

        // previous scripts' tables shift next scripts' offsets down
        input.originalFile += prevCombinedFileCount;
        if (input.originalName >= 0) input.originalName += prevCombinedNameCount;
        
        output.write(input);
      }
      
      prevCombinedFileCount += entry.fileCount;
      prevCombinedNameCount += entry.nameCount;
    }

    return output.text();
  }

}