namespace danfo.wscript.shell {

  export function run(cmdline: string): string {
    const wShell = WScript.CreateObject('WScript.Shell') as WshShell;
    const process = wShell.Exec(cmdline);
    while (process.Status === WshShell.WshStatus.WshRunning) {
      WScript.Sleep(50);
    }

    const output = process.StdOut.ReadAll();
    const err = process.StdErr.ReadAll();

    return output ? (err ? output + '\n' + err : output) : err;
  }

  export function runAndForget(cmdline: string): void {
    const wShell = WScript.CreateObject('WScript.Shell') as WshShell;
    const process = wShell.Run(cmdline, 10, false);
  }
}