namespace danfo {
  export declare function wscript(): void;

  export namespace wscript {

    for (const k in danfo.wscript) {
      if (k in bootWScript) continue;

      (bootWScript as any)[k] = (danfo.wscript as any)[k];
    }
    (danfo.wscript as any) = bootWScript;
    (wscript as any) = bootWScript;


    function bootWScript() {
      wscript.fromConsole = /cscript(\.exe)$/i.test(WScript.FullName);

      const nodePath = findNode();
      if (!nodePath) {
        WScript.Echo(
          'Node.JS not found\n\n' +
          'WScript.Name ' + WScript.Name + '\n' +
          'WScript.FullName ' + WScript.FullName + '\n' +
          'WScript.ScriptName ' + WScript.ScriptName + '\n' +
          'WScript.ScriptFullName ' + WScript.ScriptFullName);
        WScript.Quit(-1);
        return;
      }

      if (wscript.fromConsole)
        WScript.Echo('WScript: Restarting with "' + nodePath + '" "' + WScript.ScriptFullName + '"');

      shell.runAndForget('"' + nodePath + '" "' + WScript.ScriptFullName + '"');
    }

  }


  export namespace wscript {

    export var fromConsole = false;

  }

}