namespace danfo.parsing.sourcemap {

  export declare namespace concatenate {

    type SourceSpan = {
      fileName: string;
      output: string;
      sourcemap: { fileName: string, content: string };
      resolvePath?(path: string): string;
    }

    type Options =
      (
        { sourcemapFileName: string; fileName?: string; } |
        { sourcemapFileName?: string; fileName: string; }
      ) &
      {
        extraFiles?: ExtraFile[];
        preserveWhitespace?: boolean;
      }

    type ExtraFile = {
      fileName: string;
      content: string;
    }

  }

  const regex_leadingLineHasNonWhitespace = /^[^\S\r\n]*\S/;
  const regex_trailingLineHasNonWhitespace = /\S[^\S\r\n]*$/;
  const regex_anyNonWhitespace = /\S/;
  const regex_TSorTSX = /\.ts$|\.tsx$/i;
  const regex_MAP = /\.map$/i;

  export function concatenate(
    { sourcemapFileName, fileName, extraFiles, preserveWhitespace }: concatenate.Options,
    inputs: (Partial<concatenate.SourceSpan> | string | undefined)[]): concatenate.SourceSpan | string {

    if (typeof sourcemapFileName === 'undefined') {
      if (typeof fileName === 'undefined')
        fileName = 'script.js';
      sourcemapFileName = fileName.replace(regex_TSorTSX, '.js') + '.map';
    }
    else if (typeof fileName === 'undefined') {
      fileName = sourcemapFileName.replace(regex_MAP, '');
    }

    if (typeof preserveWhitespace === 'undefined')
      preserveWhitespace = false;

    let contentCombined = '';
    let emitOffsets: number[] = [];
    for (let i = 0; i < inputs.length; i++) {
      const entry = inputs[i];
      if (!entry) continue;

      if (typeof entry === 'string') {
        contentCombined =
          !contentCombined ? entry :
            preserveWhitespace ? concatenate.ensureSingleNewLineSeparator(contentCombined, entry) :
              concatenate.ensureDoubleNewLineSeparator(contentCombined, entry);
        continue;
      }

      const entryOutput = entry.output || '';

      const sourcemapMatch = concatenate.findSourcemapSuffix(entryOutput);
      const appendContent = sourcemapMatch ? entryOutput.slice(0, sourcemapMatch.offset) : entryOutput;
      contentCombined =
        !contentCombined ? appendContent :
          preserveWhitespace ? concatenate.ensureSingleNewLineSeparator(contentCombined, appendContent) :
            concatenate.ensureDoubleNewLineSeparator(contentCombined, appendContent);

      // offset needs calculation because newline may have been injected, or not
      emitOffsets[i] = contentCombined.length - appendContent.length;
    }

    const combinedLineMap = parsing.lineMap(contentCombined);
    type SourcemapWithOffsets = Sourcemap & combineMappings.MappingEntry & { entry: Partial<concatenate.SourceSpan> };
    const sourcemapObjects: SourcemapWithOffsets[] = [];
    for (let i = 0; i < inputs.length; i++) {
      const entry = inputs[i];
      if (!entry) continue;
      if (typeof entry === 'string' || !entry.sourcemap) continue;

      const offset = combinedLineMap.getLineAndCharacter(emitOffsets[i])!;

      let sourcemapJson: SourcemapWithOffsets;
      try {
        sourcemapJson = JSON.parse(entry.sourcemap.content);
      }
      catch (sourcemapParseError) {
        continue;
      }
      sourcemapJson.generatedLine = offset.ln - 1;
      sourcemapJson.generatedColumn = offset.ch - 1;
      sourcemapJson.fileCount = sourcemapJson.sources?.length || 0;
      sourcemapJson.nameCount = sourcemapJson.names?.length || 0;
      sourcemapJson.entry = entry;

      sourcemapObjects.push(sourcemapJson);
    }

    if (!sourcemapObjects.length)
      return contentCombined;
    
    const combinedMappings = combineMappings(sourcemapObjects);
    const combinedSourcemap: Sourcemap = {
      version: sourcemapObjects[0].version,
      file: fileName,
      sourceRoot: '',
      sources: [],
      names: [],
      mappings: combinedMappings,
      sourcesContent: []
    };

    for (const srcmap of sourcemapObjects) {
      for (const s of srcmap.sources) {
        let sourcePath = srcmap.sourceRoot ? srcmap.sourceRoot + '/' + s : s;
        if (typeof srcmap?.entry.resolvePath === 'function')
          sourcePath = srcmap.entry.resolvePath(sourcePath);

        combinedSourcemap.sources.push(sourcePath);
      }

      for (const n of srcmap.names) {
        combinedSourcemap.names.push(n);
      }

      for (let i = 0; i < Math.max(srcmap.sources.length, srcmap.sourcesContent.length); i++) {
        combinedSourcemap.sourcesContent.push(srcmap.sourcesContent[i] || '');
      }
    }
      

    if (extraFiles && extraFiles.length) {
      const existingFileCount = Math.max(combinedSourcemap.sources.length, combinedSourcemap.sourcesContent.length);
      for (let i = 0; i < extraFiles.length; i++) {
        const fi = extraFiles[i];
        combinedSourcemap.sources[existingFileCount + i] = fi.fileName;
        combinedSourcemap.sourcesContent[existingFileCount + i] = fi.content;
      }
    }

    const combinedSourcemapString = JSON.stringify(combinedSourcemap);
    contentCombined += '\n//# sourceMappingURL=' + sourcemapFileName;
    if (!preserveWhitespace)
      contentCombined += '\n';
    return {
      fileName,
      output: contentCombined,
      sourcemap: { fileName: sourcemapFileName, content: combinedSourcemapString }
    };
  }

  export namespace concatenate {

    const regex_anyNonWhitespace = /\S/;
    const regex_LFwhitespace_end = /\n[^\S\n]*$/;
    const regex_LFwhitespace_LFwhitespace_end = /\n[^\S\n]*\n[^\S\n]*$/;
    const regex_start_LFwhitespace = /^\n[^\S\n]*/;
    const regex_start_LFwhitespace_LFwhitespace = /^\n[^\S\n]*\n[^\S\n]*/;
    const regex_sourcemap = /(\n\s*)(\/\/#\s*sourceMappingURL=)([^\n]+?)(\s*)$/;
    const regex_any_nonNewLines = /[^\n]/;

    export function ensureSingleNewLineSeparator(text1: string, text2: string) {
      if (!regex_any_nonNewLines.test(text1) || !regex_any_nonNewLines.test(text2))
        return text1 + text2;

      if (!text1 || !regex_anyNonWhitespace.test(text1))
        return text1;

      const leadCount =
        regex_LFwhitespace_end.test(text1) ? 1 :
          0;
      
      const trailCount =
        regex_start_LFwhitespace.test(text2) ? 1 :
          0;
      
      if (leadCount + trailCount === 0)
        return text1 + '\n' + text2;
      else
        return text1 + text2;
    }


    export function ensureDoubleNewLineSeparator(text1: string, text2: string) {
      if (!regex_any_nonNewLines.test(text1) || !regex_any_nonNewLines.test(text2))
        return text1 + text2;

      if (!text1 || !regex_anyNonWhitespace.test(text1))
        return text1;

      const leadCount =
        regex_LFwhitespace_LFwhitespace_end.test(text1) ? 2 :
          regex_LFwhitespace_end.test(text1) ? 1 :
            0;
      
      const trailCount =
        regex_start_LFwhitespace_LFwhitespace.test(text2) ? 2 :
          regex_start_LFwhitespace.test(text2) ? 1 :
            0;
      
      if (leadCount + trailCount === 0)
        return text1 + '\n\n' + text2;
      else if (leadCount + trailCount === 1)
        return text1 + '\n' + text2;
      else
        return text1 + text2;
    }

    export function findSourcemapSuffix(text: string) {
      const match = regex_sourcemap.exec(text);
      if (match)
        return {
          offset: match.index,
          wholeText: match[0],
          leadWhitespace: match[1],
          sourceMappingURLPrefix: match[2],
          sourcemap: match[3],
          trailWhitespace: match[4]
        };
    }

  }
}