namespace danfo {

  export namespace parsing {
    export declare function atob(base64Encoded: string): string;
  }

  if (typeof atob === 'function') {
    parsing.atob = atob;
  }
  else if (typeof Buffer === 'function') {
    if (typeof Buffer.from === 'function') {
      parsing.atob = polyfillWithBufferFrom();
    }
    else {
      parsing.atob = polyfillWithBuffer();
    }
  }
  else {
    parsing.atob = polyfillFull();
  }

  function polyfillWithBuffer() {
    return atob;

    function atob(base64Encoded: string): string {
      return new Buffer(base64Encoded, 'base64').toString('binary');
    }
  }

  function polyfillWithBufferFrom() {
    return atob;

    function atob(base64Encoded: string): string {
      return Buffer.from(base64Encoded, 'base64').toString('binary');
    }
  }

  function polyfillFull() {
    const charToInt = createCharToInt();
    const whitespaces = [CharCodes.Space, CharCodes.TAB, CharCodes.CR, CharCodes.LF];

    return atob;
    
    function createCharToInt() {
      const charToIntStr = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';
      const charToIntArray: number[] = [];
      for (let i = 0; i < charToIntStr.length; i++) {
        charToIntArray.push(charToIntStr.charCodeAt(i));
      }

      return charToIntArray;
    }

    function atob(base64Encoded: string): string {
      let result = '';
      let acc = 0;
      let count = 0;
      for (let i = 0; i < base64Encoded.length; i++) {
        const ch = base64Encoded.charCodeAt(i);
        const bits = charToInt.indexOf(ch);

        // skip whitespace and equal signs
        // (allow equal signs in the middle because this is a limited-use polyfill)
        if (bits < 0) {
          if (ch === CharCodes.Equal || whitespaces.indexOf(ch) >= 0) continue;
          else throw new Error('Incorrect character ' + base64Encoded.charAt(i) + ' at position ' + i + '.');
        }

        acc <<= 6;
        acc |= bits;

        count++;

        if (count === 3) {
          result += String.fromCharCode((acc & 0xff0000) >> 16);
          result += String.fromCharCode((acc & 0xff00) >> 8);
          result += String.fromCharCode(acc & 0xff);
          acc = 0;
          count = 0;
        }
      }

      if (count) {
        if (count === 2) result += String.fromCharCode((acc & 0xff00) >> 8);
        result += String.fromCharCode(acc & 0xff);
      }

      return result;
    }

  }
}