namespace danfo.parsing {

  export declare namespace parseHTML {

    type ReferenceOrContent = {
      tag: 'script' | 'style' | 'link';
      type: 'css' | 'typescript' | 'javascript' | 'tsconfig' | string;

      start: number;
      length: number;
      originalLeadLength: number;
      originalTrailLength: number;
      lead: string | null;
      trail: string | null;

      reference?: string;
      content?: string;
    }
  }

  export function parseHTML(html: string): parseHTML.ReferenceOrContent[] {

    let elements: parseHTML.ReferenceOrContent[] = [];

    let pos = 0;
    while (pos < html.length - 7) { // 7 chars: < STY LE >

      pos = html.indexOf('<', pos);

      let firstLetter = html.charCodeAt(pos + 1); // next after <
      if (firstLetter === CharCodes.Exclamation) {
        if (html.charCodeAt(pos + 2) !== CharCodes.Dash) {
          pos = html.indexOf('>', pos + 2);
          if (pos < 0) break; // no angle close? finish
          pos++; // skip angle close
          continue;
        }
        else if (html.charCodeAt(pos + 3) === CharCodes.Dash) {
          // fully formed HTML comment <!-- bla bla -->
          pos = html.indexOf('-->', pos + 3);

          if (pos < 0) break; // no close comment? finish
          pos += 3; // skip close comment
          continue;
        }
        else {
          // partial HTML comment <!- bla bla ->
          pos = html.indexOf('->', pos + 2);

          if (pos < 0) break; // no close comment? finish
          pos += 2; // skip close comment
          continue;
        }
      }
      else if (firstLetter === CharCodes.Slash) {
        pos = html.indexOf('>', pos + 2);
        if (pos < 0) break;
        pos++;
        continue;
      }
      else if (isAtoZ(firstLetter)) {
        let elementEndPos = html.indexOf('>', pos + 3);
        if (elementEndPos < 0) break;
        elementEndPos++;

        if (firstLetter === CharCodes.UPPER_L || firstLetter === CharCodes.lower_l) {
          if (isElementNameAt(html, pos + 1, 'link')) {
            processLinkElement(elementEndPos);
            continue;
          }
        }
        else if (firstLetter === CharCodes.UPPER_S || firstLetter === CharCodes.lower_s) {
          if (isElementNameAt(html, pos + 1, 'script')) {
            processScriptElement(elementEndPos);
            continue;
          }
          else if (isElementNameAt(html, pos + 1, 'style')) {
            processStyleElement(elementEndPos);
            continue;
          }
        }

        pos = elementEndPos;
      }
      else {
        pos++;
      }

    }

    return elements;

    function enclosedInMatchedQuotes(start: number, end: number) {
      const firstChar = html.charCodeAt(start);
      if (firstChar === CharCodes.Quote || firstChar === CharCodes.Apostrophe) {
        const lastChar = html.charCodeAt(end - 1);
        if (lastChar === firstChar && end > start + 1) {
          return true;
        }
      }

      return false;
    }

    function processLinkElement(elementEndPos: number) {
      let seek = pos + 6 /* < LINK */;
      let lastRelPos = 0, lastRelEnd = 0, lastRelAttributePos = 0, lastRelAttributeEnd = 0;
      let lastHrefPos = 0, lastHrefEnd = 0, lastHrefAttributePos = 0, lastHrefAttributeEnd = 0;

      while (true) {
        seek = findAttributeStart(html, seek, elementEndPos);
        if (!seek)
          break;

        if (isAttributeAndEqualsAt(html, seek, 'rel')) {
          const attributeEnd = findAttributeEnd(html, seek + 4 /* REL= */, elementEndPos - 1);
          if (attributeEnd < 0) {
            pos = elementEndPos;
            break;
          }

          let relPos = seek + 4 /* REL= */;
          let relEnd = attributeEnd;

          if (enclosedInMatchedQuotes(relPos, relEnd)) {
            relPos++;
            relEnd--;
          }

          lastRelPos = relPos;
          lastRelEnd = relEnd;
          lastRelAttributePos = seek;
          lastRelAttributeEnd = attributeEnd;

          seek = attributeEnd;
        }
        else if (isAttributeAndEqualsAt(html, seek, 'href')) {
          const attributeEnd = findAttributeEnd(html, seek + 5 /* HREF= */, elementEndPos - 1);
          if (attributeEnd < 0) {
            pos = elementEndPos;
            break;
          }

          let hrefPos = seek + 5 /* HREF= */;
          let hrefEnd = attributeEnd;
          if (enclosedInMatchedQuotes(hrefPos, hrefEnd)) {
            hrefPos++;
            hrefEnd--;
          }

          lastHrefPos = hrefPos;
          lastHrefEnd = hrefEnd;
          lastHrefAttributePos = seek;
          lastHrefAttributeEnd = attributeEnd;

          seek = attributeEnd;
        }
        else {
          const attributeEnd = findAttributeEnd(html, seek + 1 /* at least one letter attribute */, elementEndPos - 1);
          if (attributeEnd < 0) {
            pos = elementEndPos;
            break;
          }

          seek = attributeEnd;
        }
      }

      if (!lastRelPos || !lastHrefPos) return;

      const rel = decodeEntities(html.slice(lastRelPos, lastRelEnd));
      if (!/stylesheet/i.test(rel)) return;

      const href = decodeEntities(html.slice(lastHrefPos, lastHrefEnd));

      elements.push({
        tag: 'link',
        type: 'css',
        start: pos, length: elementEndPos - pos,
        reference: href,
        originalLeadLength: elementEndPos - pos,
        originalTrailLength: 0,
        lead:
          '<style' + sliceElementWithAttributeHoles(
            html, pos + 5 /* < LINK */, elementEndPos,
            lastRelAttributePos, lastRelAttributeEnd,
            lastHrefAttributePos, lastHrefAttributeEnd),
        trail: '</' + 'style' + '>'
      });

      pos = elementEndPos;
    }

    function processStyleElement(elementEndPos: number) {
      let seekClose = elementEndPos;
      let closeElementPos = 0;
      let closeElementEnd = 0;
      while (seekClose < html.length) {
        closeElementPos = html.indexOf('</', seekClose); // may not be STYLE element end
        if (closeElementPos < 0) break; // incomplete element, ignore

        if (!isElementNameAt(html, closeElementPos + 2, 'style')) { // not STYLE element close - keep seeking
          seekClose = closeElementPos + 2;
          continue;
        }

        closeElementEnd = html.indexOf('>', closeElementPos + 7 /* </ STY LE */);
        if (closeElementEnd > 0)
          closeElementEnd++;
        break;
      }

      if (closeElementEnd > 0) {
        elements.push({
          tag: 'style',
          type: 'css',
          start: pos, length: closeElementEnd - pos,
          content: html.slice(elementEndPos, closeElementPos),
          originalLeadLength: elementEndPos - pos,
          originalTrailLength: closeElementEnd - closeElementPos,
          lead: html.slice(pos, elementEndPos), trail: html.slice(closeElementPos, closeElementEnd)
        });

        pos = closeElementEnd;
      }
      else {
        // STYLE not closed, finish
        pos = html.length;
      }
    }

    function processScriptElement(elementEndPos: number) {

      let seek = pos + 7 /* < SCR IPT */;
      let lastSrcPos = 0, lastSrcEnd = 0, lastSrcAttributePos = 0, lastSrcAttributeEnd = 0;
      let lastTypePos = 0, lastTypeEnd = 0, lastTypeAttributePos = 0, lastTypeAttributeEnd = 0;

      while (true) {
        seek = findAttributeStart(html, seek, elementEndPos);
        if (!seek)
          break;

        if (isAttributeAndEqualsAt(html, seek, 'src')) {
          const attributeEnd = findAttributeEnd(html, seek + 4 /* SRC= */, elementEndPos - 1);
          if (attributeEnd < 0) {
            seek = elementEndPos;
            break;
          }

          let srcPos = seek + 4 /* SRC= */;
          let srcEnd = attributeEnd;
          if (enclosedInMatchedQuotes(srcPos, srcEnd)) {
            srcPos++;
            srcEnd--;
          }

          lastSrcPos = srcPos;
          lastSrcEnd = srcEnd;
          lastSrcAttributePos = seek;
          lastSrcAttributeEnd = attributeEnd;

          seek = attributeEnd;
        }
        else if (isAttributeAndEqualsAt(html, seek, 'type')) {
          const attributeEnd = findAttributeEnd(html, seek + 5 /* TYPE= */, elementEndPos - 1);
          if (attributeEnd < 0) {
            seek = elementEndPos;
            break;
          }

          let typePos = seek + 5 /* TYPE= */;
          let typeEnd = attributeEnd;
          if (enclosedInMatchedQuotes(typePos, typeEnd)) {
            typePos++;
            typeEnd--;
          }

          lastTypePos = typePos;
          lastTypeEnd = typeEnd;
          lastTypeAttributePos = seek;
          lastTypeAttributeEnd = attributeEnd;

          seek = attributeEnd;
        }
        else {
          const attributeEnd = findAttributeEnd(html, seek + 1 /* one letter attribute at least */, elementEndPos - 1);
          if (attributeEnd < 0) {
            seek = elementEndPos;
            break;
          }

          seek = attributeEnd;
        }
      }

      let src = lastSrcPos ? decodeEntities(html.slice(lastSrcPos, lastSrcEnd)) : null;
      let type = lastTypePos ? decodeEntities(html.slice(lastTypePos, lastTypeEnd)) : null;


      let seekClose = elementEndPos;
      let closeElementPos = 0;
      let closeElementEnd = 0;
      while (seekClose < html.length) {
        closeElementPos = html.indexOf('</', seekClose); // may not be SCRIPT element end
        if (closeElementPos < 0) break; // incomplete element, ignore

        if (!isElementNameAt(html, closeElementPos + 2, 'script')) { // not SCRIPT element close - keep seeking
          seekClose = closeElementPos + 2;
          continue;
        }

        closeElementEnd = html.indexOf('>', closeElementPos + 8 /* </ SCR IPT */);
        if (closeElementEnd > 0)
          closeElementEnd++;
        break;
      }

      if (closeElementEnd > 0) {
        if (type) {
          if (parseHTML.isJavascriptType(type)) {
            type = 'javascript';
          }
          else if (parseHTML.isTypeScriptType(type)) {
            type = 'typescript';
          }
          else if (parseHTML.isTsconfigType(type)) {
            type = 'tsconfig';
          }
        }
        else {
          if (src && parseHTML.hasTypeScriptFileExtension(src))
            type = 'typescript';
          else if (src && parseHTML.isTsconfigJsonFileName(src))
            type = 'tsconfig';
          else
            type = 'javascript';
        }

        let lead = sliceElementWithAttributeHoles(
          html, pos, elementEndPos,
          lastSrcAttributePos, lastSrcAttributeEnd,
          // exclude type attribute for typescript (it's translated into javascript)
          type === 'typescript' || type === 'tsconfig' ? lastTypeAttributePos : 0,
          type === 'typescript' || type === 'tsconfig' ? lastTypeAttributeEnd : 0);

        if (src) {
          elements.push({
            tag: 'script',
            type,
            start: pos, length: closeElementEnd - pos,
            reference: src,
            originalLeadLength: elementEndPos - pos,
            originalTrailLength: closeElementEnd - closeElementPos,
            lead,
            trail: html.slice(closeElementPos, closeElementEnd)
          });
        }
        else {
          elements.push({
            tag: 'script',
            type,
            start: pos, length: closeElementEnd - pos,
            content: html.slice(elementEndPos, closeElementPos),
            originalLeadLength: elementEndPos - pos,
            originalTrailLength: closeElementEnd - closeElementPos,
            lead,
            trail: html.slice(closeElementPos, closeElementEnd)
          });
        }

        pos = closeElementEnd;
      }
      else {
        // STYLE not closed, finish
        pos = html.length;
      }
    }

  }

  function findAttributeEnd(html: string, pos: number, end: number): number {
    if (end === pos) return -1;

    let firstChar = html.charCodeAt(pos);

    for (let i = pos + 1; i < end; i++) {
      let ch = html.charCodeAt(i);
      if (firstChar === CharCodes.Quote) {
        if (ch === CharCodes.Quote) return i + 1;
      }
      else if (firstChar === CharCodes.Apostrophe) {
        if (ch === CharCodes.Apostrophe) return i + 1;
      }
      else if (isWhitespace(ch)) {
        return i;
      }
    }

    return end;
  }

  function isWordAt(text: string, pos: number, wordLowercase: string): boolean {
    for (let i = 0; i < wordLowercase.length; i++) {
      let ch = text.charCodeAt(pos + i);
      let expectChLower = wordLowercase.charCodeAt(i);
      if (ch !== expectChLower && ch + CharCodes.MakeLower !== expectChLower) return false;
    }

    return true;
  }

  function isElementNameAt(html: string, pos: number, elementName: string): boolean {
    if (!isWordAt(html, pos, elementName)) return false;
    let afterWordCh = html.charCodeAt(pos + elementName.length);
    if (isWhitespace(afterWordCh) || afterWordCh === CharCodes.AngleClose)
      return true;
    else
      return false;
  }

  function isAttributeAndEqualsAt(html: string, pos: number, attrName: string) {
    if (!isWordAt(html, pos, attrName)) return false;
    let afterWordCh = html.charCodeAt(pos + attrName.length);
    if (afterWordCh === CharCodes.Equal)
      return true;
    else
      return false;
  }

  function isWhitespace(ch: number) {
    return ch === CharCodes.Space ||
      ch === CharCodes.CR ||
      ch === CharCodes.LF ||
      ch === CharCodes.TAB;
  }

  function isAtoZ(ch: number) {
    return (ch >= CharCodes.UPPER_A && ch <= CharCodes.UPPER_Z) ||
      (ch >= CharCodes.lower_a && ch <= CharCodes.lower_z);
  }

  function findAttributeStart(html: string, pos: number, end: number): number {
    for (let i = pos; i < end; i++) {
      let ch = html.charCodeAt(i);
      while (isWhitespace(ch)) {
        i++;
        if (i >= end) return 0;
        ch = html.charCodeAt(i);
      }

      if (isAtoZ(ch))
        return i;
    }

    return 0;
  }

  function findAnyOf(text: string, pos: number, end: number, setOfChars: string) {
    for (let i = pos; i < end; i++) {
      let ch = text.charCodeAt(i);
      for (let j = 0; j < setOfChars.length; j++) {
        let match = setOfChars.charCodeAt(j);
        if (ch === match)
          return i;
      }
    }
    return -1;
  }

  function sliceElementWithAttributeHoles(
    html: string, pos: number, end: number,
    attr1Pos: number, attr1End: number,
    attr2Pos: number, attr2End: number) {

    if (attr1Pos) {
      while (attr1Pos > pos && isWhitespace(html.charCodeAt(attr1Pos - 1))) {
        attr1Pos--;
      }
    }

    if (attr2Pos) {
      while (attr2Pos > pos && isWhitespace(html.charCodeAt(attr2Pos - 1))) {
        attr2Pos--;
      }
    }

    if (attr1Pos) {
      if (attr2Pos) {
        return attr1Pos < attr2Pos ?
          html.slice(pos, attr1Pos) + html.slice(attr1End, attr2Pos) + html.slice(attr2End, end) :
          html.slice(pos, attr2Pos) + html.slice(attr2End, attr1Pos) + html.slice(attr1End, end);
      }
      else {
        return html.slice(pos, attr1Pos) + html.slice(attr1End, end);
      }
    }
    else if (attr2Pos) {
      return html.slice(pos, attr2Pos) + html.slice(attr2End, end);
    }
    else {
      return html.slice(pos, end);
    }
  }

  export namespace parseHTML {

    const regex_javascriptType = /^\s*(javascript|jscript|js|jsx)\s*$/i;
    const regex_typescriptType = /^\s*(typescript|ts|tsx)\s*$/i;
    const regex_typescriptFileExtension = /\.(ts|tsx)$/i;
    const regex_tsconfigJsonFileName = /(^|\\|\/)tsconfig\.json$/i;
    const regex_tsconfigJsonType = /^\s*(tsconfig)\s*$/i;

    export function isJavascriptType(type: string) {
      return regex_javascriptType.test(type);
    }

    export function isTypeScriptType(type: string) {
      return regex_typescriptType.test(type);
    }

    export function hasTypeScriptFileExtension(file: string) {
      return regex_typescriptFileExtension.test(file);
    }

    export function isTsconfigJsonFileName(file: string) {
      return regex_tsconfigJsonFileName.test(file);
    }

    export function isTsconfigType(type: string) {
      return regex_tsconfigJsonType.test(type);
    }
  }
}