namespace danfo.node.server {

  export declare namespace run {

    type Options = {
      fileSystem: danfo.node.FileSystemWithBinary;
      projectDir: string;
      distDir: string;
      host?: string;
      port?: number;
    }

  }

  export type Server = {
    distDir: string;
    close(): void;
  }

  export namespace extensionContentTypes {
    export const html = 'text/html';
    export const gif = 'image/gif';
    export const jpg = 'image/jpeg';
    export const js = 'application/javascript';
  }

  export function run(options: run.Options): Server {
    const http = require('http') as typeof import('http');
    const URL = require('url') as typeof import('url');
    const srv = http.createServer({}, requestListener);
    srv.listen(typeof options.port === 'number' ? options.port : 8080);

    let resolvedDistDir = options.fileSystem.resolvePath(options.distDir);
    let server = {
      distDir: options.distDir,
      close: closeServer
    };

    return server;

    function closeServer() {
      srv.close();
    }

    async function requestListener(req: import('http').IncomingMessage, res: import('http').ServerResponse): Promise<void> {
      try {
        resolvedDistDir = options.fileSystem.resolvePath(server.distDir);

        const parsedUrl = URL.parse('http://' + req.headers.host + req.url!);
        const path = (parsedUrl.path || '/').replace(/^\//, '');
        let resolvedPath = options.fileSystem.resolvePath(resolvedDistDir, path);

        if (resolvedPath.indexOf(resolvedDistDir) !== 0) {
          await reject404(req, res, parsedUrl, resolvedPath, 'Request trying to escape beyond home directory.');
          return;
        }

        let stat = options.fileSystem.stat(resolvedPath);
        if (!stat) {
          await reject404(req, res, parsedUrl, resolvedPath);
          return;
        }

        if (stat.isDirectory) {
          const resolvedIndexPath = options.fileSystem.resolvePath(resolvedPath, 'index.html');
          const indexStat = options.fileSystem.stat(resolvedIndexPath);
          if (indexStat && !indexStat.isDirectory) {
            resolvedPath = resolvedIndexPath;
            stat = indexStat;
          }
        }

        if (stat.isDirectory)
          await renderDirectoryList(req, res, parsedUrl, resolvedPath);
        else
          await renderFile(req, res, parsedUrl, resolvedPath);
      }
      catch (error) {
        await wrapRequestError(error, req, res);
      }
      finally {
        if (typeof req.destroy === 'function' && !req.destroyed)
          req.destroy();
        if (typeof res.destroyed === 'function' && !res.destroyed)
          res.destroy();
      }
    }

    async function renderFile(req: import('http').IncomingMessage, res: import('http').ServerResponse, parsedUrl: import('url').UrlWithStringQuery, resolvedFilePath: string): Promise<void> {
      const buffer = options.fileSystem.readFile(resolvedFilePath, 'binary');
      if (!buffer) {
        await reject404(req, res, parsedUrl, resolvedFilePath);
        return;
      }

      res.statusCode = 200;
      res.statusMessage = 'OK';

      const ext = resolvedFilePath.split('.').reverse().filter(part => part)[0];
      const contentType = (extensionContentTypes as any)[ext];
      if (contentType) {
        res.setHeader('Content-Type', contentType);
      }

      console.log(
        node.colors.green + 'HTTP 200 ' + req.method + ' ' + resolvedFilePath + node.colors.reset +
        node.colors.brightGreen + ' ' + (contentType || ext) + ' ' + buffer.length + node.colors.reset +
        node.colors.green + ' byte' + (buffer.length === 1 ? '' : 's') + node.colors.reset);

      await new Promise<void>(resolve =>
        res.end(buffer, resolve));
    }

    async function renderDirectoryList(req: import('http').IncomingMessage, res: import('http').ServerResponse, parsedUrl: import('url').UrlWithStringQuery, resolvedDirPath: string): Promise<void> {
      const dirList = options.fileSystem.readDirectory(resolvedDirPath);
      if (!dirList) {
        await reject404(req, res, parsedUrl, resolvedDirPath);
        return;
      }

      console.log(
        node.colors.green + 'HTTP 200 ' + req.method + ' ' + parsedUrl.path + ' -> ' + resolvedDirPath + node.colors.reset +
        node.colors.brightGreen + dirList.length + node.colors.reset +
        node.colors.green + (dirList.length === 1 ? ' entry' : 'entries') + node.colors.reset);


      res.statusCode = 200;
      res.statusMessage = 'OK';

      let dirRenderHTML =
        '<html><head><title>' + resolvedDistDir + ': ' + dirList.length + ' file' + (dirList.length === 1 ? '' : 's') + '</title></head>' +
        '<body>' +
        '<h2>' + resolvedDistDir + ': ' + dirList.length + ' file' + (dirList.length === 1 ? '' : 's') + '</h2>' +
        '<ul>' +
        (resolvedDirPath === resolvedDistDir ?
          '' :
          '<li><a href="..">..</a>') +
        dirList.map(entry => {
          const baseName = danfo.build.getBaseName(entry);
          return '<li><a href="' + baseName+ '">' + baseName + '</a>';
        }).join('\n') +
        '</ul>' +
        '</body></html>';

      await endRequestWithHTML(res, dirRenderHTML);
    }

    async function reject404(req: import('http').IncomingMessage, res: import('http').ServerResponse, parsedUrl: import('url').UrlWithStringQuery, resolvedPath: string, additionalMessage?: string) {

      res.statusCode = 404;

      const errorMessage =
        typeof additionalMessage === 'string' ?
          additionalMessage + ' for ' + resolvedPath :
          'File not found: ' + resolvedPath;

      res.statusMessage = errorMessage;

      console.log(
        node.colors.green + 'HTTP ' + node.colors.reset + node.colors.red + '404 ' + node.colors.reset + node.colors.green + req.method + ' ' + parsedUrl.path + ' -> ' + resolvedPath + node.colors.reset +
        (additionalMessage ? ' ' + node.colors.brightGreen + additionalMessage + node.colors.reset : ''));
      
      await endRequestWithHTML(
        res,
        '<html><head><title>' + errorMessage + '</title><head><body>' +
        '<h2 style="color: firebrick">' + errorMessage + '</h2>' +
        'at ' + Date() +
        '</body></html>'
      );
    }

    async function endRequestWithHTML(res: import('http').ServerResponse, html: string | Buffer, contentType: string = 'text/html') {
      try { res.setHeader('Content-Type', contentType); }
      catch (error) { /* ignore content type failure */ }

      await new Promise<void>(resolve => res.end(html, resolve));
    }

    async function wrapRequestError(error: any, req: import('http').IncomingMessage, res: import('http').ServerResponse): Promise<void> {
      try {
        if (req.destroyed || res.destroyed)
          return;

        const errorMessage =
          error && error.message ? String(error.message) :
            error ? String(error) :
              'Internal failure';
        
        res.statusCode = 500;
        res.statusMessage = errorMessage;

        console.log(
          node.colors.green + 'HTTP ' + node.colors.reset + node.colors.red + '500 ' + node.colors.reset + node.colors.green + req.method + ' ' + req.url + node.colors.reset +
          node.colors.brightGreen + ' ' + errorMessage+ node.colors.reset);

        
        await endRequestWithHTML(
          res,
          '<html><head><title>' + errorMessage + '</title><head><body>' +
          '<h2 style="color: firebrick">' + errorMessage + '</h2>' +
          'at ' + Date() +
          '</body></html>');
      }
      catch (secondaryError) {
        // oops that is too bad?
      }
      finally {
        if (typeof req.destroy === 'function' && !req.destroyed)
          req.destroy(error);
        if (typeof res.destroyed === 'function' && !res.destroyed)
          res.destroy(error);
      }
    }
  }

}