namespace danfo.node {

  export declare namespace formatBuildError {

    type Options = {
      typescriptForFormatting: typeof import('typescript');
      fileSystem: { resolvePath(path: string): string };
      projRoot: string;
    }
  }

  export function formatBuildError(err: build.code.BuildError, { typescriptForFormatting, fileSystem, projRoot }: formatBuildError.Options) {
    if (err.typescriptDetails) {
      try {
        if (!typescriptForFormatting)
          typescriptForFormatting = require('typescript') as typeof import('typescript');

        const formatted = typescriptForFormatting.formatDiagnosticsWithColorAndContext([err.typescriptDetails], {
          getCanonicalFileName: file => fileSystem.resolvePath(file),
          getCurrentDirectory: () => projRoot,
          getNewLine: () => '\n'
        }).replace(/^\n+/, '').replace(/\n+$/, '');
        return formatted;
      }
      catch (tsFormatError) { }
    }

    return err.code + ' ' + err.message;
  }
}