namespace danfo.parsing.sourcemap {

  const integerToChar = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/=';
  const charToInteger = 'i   j_`abcdefgh   k   +,-./0123456789:;<=>?@ABCD      EFGHIJKLMNOPQRSTUVWXYZ[\\]^';
  const baseChar = 43;
  
  export function decodeVLQSpanToArray(string: string, start = 0, end = string.length, resultArray: number[]): number {
    let shift = 0;
    let value = 0;

    let resultArrayOffset = 0;
    for (let i = start; i < end; i += 1) {
      const ch = string.charCodeAt(i);

      let integer = charToInteger.charCodeAt(ch - baseChar) - baseChar;

      if (!(integer>=0))
        throw new Error('Invalid character "' + string.charAt(i) + '" at position ' + i + ': ' + string.slice(start, end) + '.');

      const hasContinuationBit = integer & 32;

      integer &= 31;
      value += integer << shift;

      if (hasContinuationBit) {
        shift += 5;
      } else {
        const shouldNegate = value & 1;
        value >>>= 1;

        if (shouldNegate)
          resultArray[resultArrayOffset] = value === 0 ? -0x80000000 : -value;
        else
          resultArray[resultArrayOffset] = value;
        
        resultArrayOffset++;

        // reset
        value = shift = 0;
      }
    }

    return resultArrayOffset;
  }

  export function encodeVLQArray(value: number[], start?: number, end?: number): string;
  export function encodeVLQArray(value: number[], start: number, end: number, result: { [byteIndex: number]: number }, offset: number): number;
  export function encodeVLQArray(value: number[], start = 0, end = value.length, result?: { [byteIndex: number]: number }, offset?: number): string | number {
    let resultStr = '';
    let bufSize = 0;
    for (let i = 0; i < end; i += 1) {
      let num = value[i];

      if (num < 0) num = (-num << 1) | 1;
      else num <<= 1;

      do {
        let clamped = num & 31;
        num >>>= 5;

        if (num > 0) clamped |= 32;

        if (result) result[offset! + bufSize] = integerToChar.charCodeAt(clamped);
        else resultStr += integerToChar.charAt(clamped);
        bufSize++;
      } while (num > 0);
    }

    if (result)
      return bufSize;
    else
      return resultStr;
  }

}