/** needed for creepy @types/node syntax */
declare interface Set<T> { }

namespace danfo {

  export declare function node(): void;

  export namespace node {

    for (const k in danfo.node) {
      if (k in bootNode) continue;

      (bootNode as any)[k] = (danfo.node as any)[k];
    }
    (danfo.node as any) = bootNode;
    (node as any) = bootNode;

    function bootNode() {

      if (!process?.stdout?.isTTY) {
        for (const k in node.colors) {
          if (typeof (node.colors as any)[k] === 'string')
            (node.colors as any)[k] = '';
        }
      }

      let cmdline: danfo.parsing.cmdline.parseCmdline.Cmdline;

      {
        let cmd = danfo.parsing.cmdline.parseCmdline(process);
        if (!cmd) {
          // must have been invoked as a library, not a command line tool
          return;
        }

        if (cmd === 'worker')
          return runWorker();
        else
          cmdline = cmd;
      }

      console.log(node.colors.yellow + '  v' + danfo.versionStr + node.colors.reset);

      if (cmdline.dumpCmdLine) {
        const _dumpCmdline = { ...cmdline } as any;
        if (_dumpCmdline.packageJson)
          _dumpCmdline.packageJson = '{...}';
        console.log('cmdline ', _dumpCmdline);
      }


      if (cmdline.knownArguments && cmdline.knownArguments.help) {
        console.log(helpText());
      }

      if (cmdline.knownArguments && cmdline.knownArguments.version) {
        console.log(versionStr);
      }

      if (cmdline.errors) {
        for (let i = 0; i < cmdline.errors.length; i++) {
          console.log(node.colors.red + 'ERROR ' + cmdline.errors[i] + node.colors.reset);
        }

        process.exit(1); // error exit code 1
        return;
      }

      runAsync().then(() => {
        // TODO: finished.
      }, error => {
        console.error(error);
        process.exit(2); // error exit code 2 (crash not just incorrect arguments)
      });

      return;

      async function runAsync() {
        let serverUp: { options: danfo.node.server.run.Options, server: danfo.node.server.Server } | undefined;

        if (cmdline.scaffold) {
          performScaffold();
        }

        if (cmdline.build) {
          const startRootDir = npmLifecycleCurrentDirectory();
          const trackingFileSystem = createFS(startRootDir);
          const nonTrackingFileSystem = createFS(startRootDir);
          nonTrackingFileSystem.stopWatching();

          let firstBuild = true;
          const fs = require('fs') as typeof import('fs');
          danfo.build.projectBuilder({
            projRoot:
              cmdline.projRoot ||
              (cmdline.sources?.length ? void 0 : startRootDir), // if neither sources nor project root specified, use current directory
            trackingFileSystem,
            sources: cmdline.sources || [],
            outputPath: cmdline.outputPath,
            watch: cmdline.watch,
            onBuildStart: buildContext => {
              if (firstBuild) {
                if (cmdline.server) {
                  const projectDirWithFallback = buildContext.projectDir || startRootDir;
                  nonTrackingFileSystem.fs.basePath = projectDirWithFallback;
                  serverUp = startServer(nonTrackingFileSystem.fs, projectDirWithFallback, buildContext.outputDirPath);
                }
                process.stdout.write(node.colors.cyan + 'Build...' + node.colors.reset);
              }
              else {
                const changes: string[] = [];
                if (buildContext.changes) {
                  for (const ch of buildContext.changes) {
                    if (ch) changes.push(ch);
                  }
                }
                const maxChangeOneLine = 3;
                const maxChangeCount = 5;
                if (!changes.length) {
                  process.stdout.write('\n' + node.colors.cyan + 'Rebuild [unknown changes]...' + node.colors.reset);
                }
                else if (changes.length <= maxChangeOneLine) {
                  const filenames: string[] = [];
                  for (const ch of buildContext.changes!) {
                    filenames.push(node.colors.brightCyan + danfo.build.getBaseName(ch).replace(/\\/g, '/')) + node.colors.reset;
                  }
                  const rebuildText = '\n' + node.colors.cyan + 'Rebuild [' + node.colors.reset + filenames.join(node.colors.cyan + ', ' + node.colors.reset) + node.colors.cyan + ' changed]...' + node.colors.reset;
                  process.stdout.write(rebuildText);
                }
                else {
                  let rebuildText = '\n' + node.colors.cyan + 'Rebuild [' + node.colors.brightCyan + buildContext.changes!.length + node.colors.cyan + ' changes]:' + node.colors.reset;
                  for (let i = 0; i < maxChangeCount; i++) {
                    const moreLast = i < changes.length && i === maxChangeCount - 1;
                    const ch = buildContext.changes![moreLast ? buildContext.changes!.length - 1 : i];
                    const relPath = trackingFileSystem.fs.getRelativePath(buildContext.projectDir || trackingFileSystem.fs.basePath, ch).replace(/\\/g, '/');
                    const lastSlashPos = relPath.lastIndexOf('/');
                    const lead = lastSlashPos >= 0 ? relPath.slice(0, lastSlashPos + 1) : '';
                    const trail = lastSlashPos >= 0 ? relPath.slice(lastSlashPos + 1) : relPath;
                    rebuildText += '\n  ' + node.colors.cyan + '< ' + (moreLast ? '... ' : '') + lead + node.colors.brightCyan + trail + node.colors.reset;
                  }
                  rebuildText += '\n' + node.colors.cyan + '  ...' + node.colors.reset;
                  process.stdout.write(rebuildText);
                }
              }
            },
            onBuildFinish: buildContext => {
              const projectDirWithFallback = buildContext.projectDir || startRootDir;
              nonTrackingFileSystem.fs.basePath = projectDirWithFallback;
              if (serverUp) {
                serverUp.server.distDir = buildContext.outputDirPath;
              }

              if (firstBuild) {
                if (buildContext.html && cmdline.server !== false)
                  serverUp = startServer(nonTrackingFileSystem.fs, projectDirWithFallback, buildContext.outputDirPath);
                
                // TODO: whatever else (start browser maybe?)
              }
              
              firstBuild = false;

              let builtText = ' ' + node.colors.cyan + ((buildContext.buildTime / 100) | 0) / 10 + 's.' + node.colors.reset;
              if (buildContext.errors.length) {
                builtText += '\n' + node.colors.red + '>> ' + (buildContext.errors.length === 1 ? 'ERROR' : buildContext.errors.length + ' ERRORS') + ' <<' + node.colors.reset;
                for (const err of buildContext.errors) {
                  if (typeof err.format === 'function') {
                    builtText += '\n' + err.format();
                  }
                  else {
                    builtText += '\n' + err.message;
                  }
                }
              }

              if (buildContext.emits.length) {
                for (const em of buildContext.emits) {
                  const path = (buildContext.projectDir ? trackingFileSystem.fs.getRelativePath(buildContext.projectDir, em.fullPath) : em.localPath)
                    .replace(/\\/g, '/');

                  if (!em.changedFrom) {
                    builtText += '\n' + node.colors.grey + '  > ' + path + ' (no change)' + node.colors.reset;
                  }
                  else {
                    if (typeof em.changedFrom.content !== 'string') {
                      builtText += '\n  > ' + path + ' > NEW';
                    }
                    else {
                      builtText += '\n  > ' + path + ' > CHANGED' + node.colors.grey + ' AFTER ' + (((Date.now() - em.changedFrom.timestamp!) / 100) | 0) / 10 + 's' + node.colors.reset;
                    }
                    createParentDirectoryRecursive(em.fullPath);
                    trackingFileSystem.fs.writeFile(em.fullPath, em.content as string);
                  }
                }
              }

              console.log(builtText);
            }
          });
        }

        // END of runAsync here

        function startServer(fileSystem: FileSystemWithBinary, projDir: string, outputDirPath: string) {
          if (serverUp) return serverUp;
          const host = (cmdline.server ? cmdline.server.host : void 0) || 'localhost';
          const port = (cmdline.server ? cmdline.server.port : void 0) || danfo.parsing.cmdline.derivePort(projDir);
          const options: danfo.node.server.run.Options = {
            distDir: outputDirPath,
            fileSystem,
            projectDir: cmdline.projRoot || '.',
            host,
            port
          };
          const server = danfo.node.server.run(options);

          process.stdout.write(
            ' ' + node.colors.brightBlue + ((cmdline.server ? cmdline.server.https : false) ? 'https://' : 'http://') + options.host + ':' + options.port + '/' + node.colors.reset
          );

          return { options, server };
        }

        function createParentDirectoryRecursive(filePath: string) {
          const path = require('path') as typeof import('path');
          const fs = require('fs') as typeof import('fs');

          const dirPath = path.dirname(filePath);

          if (fs.existsSync(dirPath)) return;
          createParentDirectoryRecursive(dirPath)
          fs.mkdirSync(dirPath);
        }

      }
    }

  }

  function npmLifecycleCurrentDirectory() {
    return (
      process.env.INIT_CWD ||
      (process.env.npm_lifecycle_event ?
        (require('path') as typeof import('path')).resolve(__dirname, '../..') :
        process.cwd()
      )
    );
  }

}