namespace danfo.parsing {

  export declare namespace lineMap {
    type Output = {
      getLineAndCharacter(pos: number): { ln: number, ch: number } | null
    }
  }

  export function lineMap(text: string): lineMap.Output {
    const textCopy = String(text);

    return {
      getLineAndCharacter
    };

    function getLineAndCharacter(pos: number): { ln: number, ch: number } | null {
      if (pos < 0 || pos > textCopy.length)
        return null;

      let lineStartPos = 0;
      let line = 1;
      let currentPos = 0;
      const lineBreakRegExp = /\r\n|\n|\r/gm;
      while (true) {
        const nextLineBreak = lineBreakRegExp.exec(textCopy);
        if (!nextLineBreak)
          return {
            ln: line,
            ch: pos - lineStartPos  + 1
          };

        if (nextLineBreak.index >= pos) {
          return {
            ln: line,
            ch: pos - lineStartPos + 1
          };
        }

        line++;
        lineStartPos = nextLineBreak.index + nextLineBreak[0].length;
      }
    }
  }

}