namespace danfo.wscript {

  export function findNode() {
    const simpleWhereProbes = shell.run('cmd /c where node').replace(/^\s*/, '').replace(/\s*$/, '').split(/(\r\n)|\r|\n/g);
    if (simpleWhereProbes.length && simpleWhereProbes[0])
      return simpleWhereProbes[0];
    
    return null;
  }

}